import React, { useEffect, useState } from 'react'
import './header.css'
import profile from '../img/profile.png'
import { NavLink,Link, useHistory, useParams } from 'react-router-dom'
import './sidemenu/sidemenu.css';
import logo from '../img/logo.png'
import axios from 'axios';

const LogoutHeader = () => {
    const params = useParams()
    const history = useHistory()
    const[userId, setUserId] = useState('')
    const[isAdmin, setIsAdmin] = useState(false)
    let id = params.id
  
  const getUserProfile = async() => {
      await axios.get(`/userProfile`)
      .then((res) => {
          if(res.status===200){
            const data = res.data;
            setIsAdmin(data.isAdmin&&data.isAdmin)
          }
      }).catch((err) => {
          console.log(err)
        //   history.push("/login")
      })
  }

  useEffect(() => {
    const Id = JSON.parse(localStorage.getItem("currUserId"))

        console.log(Id)
        setUserId(Id)
    getUserProfile()
  }, [])


    const setshowside=(e)=>{
        document.querySelector('.sidemnu').style.display='block';
       e.preventDefault()
    }
    const setsidemenu=(e)=>{
        document.querySelector('.sidemnu').style.display='none';
        e.preventDefault()
      }

      const logoutNow = () => {
        localStorage.clear()
      }

  return (
    <div>
         <div className='header'>
         <Link to='/' className='logo-menu'>
            <div className='logo'>
                <img src={logo} alt='eeve Project' />
                <h6>COLLABORATIVE AUTOMOBILE MANUFACTURING</h6>
            </div>
           </Link>
           <div className='headermenu'>
              <div className='headersearch'>
                  <form>
                      <input type='text' name='search' placeholder='Search' />
                  </form>
              </div>
              <div className='language'>
                  <form>
                      <select>
                          <option>EN</option>
                          <option>GER</option>
                      </select>
                  </form>
              </div>
              <div className='profile'>
                  {userId?
                  <Link to='/login' className='home-login-btn'>
                    <button onClick={logoutNow}>Logout</button>
                </Link>:
                  <Link to='/login' className='home-login-btn'>
                      <button>Sign in</button>
                  </Link>}
                  <img src={profile} alt='profileicon' />
                 
              </div>
              <div className='hammenu'>
                <svg width="26" height="18" viewBox="0 0 26 18" fill="none" xmlns="http://www.w3.org/2000/svg" onClick={setshowside}>
                    <rect y="0.5" width="26" height="3" fill="#BDBDBD"/>
                    <rect y="7.5" width="26" height="3" fill="#BDBDBD"/>
                    <rect y="14.5" width="26" height="3" fill="#BDBDBD"/>
                </svg>
              {/* {showside && <SideMenu />} */}
              </div>
           </div>
        </div> 
        <div className='sidemnu'>
            <h6 onClick={setsidemenu}>&times;</h6>
          <ul>
            {isAdmin?
                <li>
                    <NavLink to={`/dashboard/${userId}`} className='sidelink' activeClassName='active'> DDashboard </NavLink>
                </li>:
                ''
            }
            <li>
                <NavLink to="/" className='sidelink' activeClassName='active'> Home </NavLink>
            </li>
            <li>
                <NavLink to="/designgallery" className='sidelink' activeClassName='active'> Design Gallery </NavLink>
            </li>
            <li>
                <NavLink to="/partgallery" className='sidelink' activeClassName='active'> Part Gallery  </NavLink>
            </li>
            <li>
                <NavLink to="/cadgallery" className='sidelink' activeClassName='active'> CAD Gallery </NavLink>
            </li>
            <li>
                <NavLink to="/faq" className='sidelink' activeClassName='active'> FAQ </NavLink>
            </li>
            <li>
                <NavLink to="/setting" className='sidelink' activeClassName='active'> Setting </NavLink>
            </li>
          </ul>
       </div>
    </div>
  )
}

export default LogoutHeader