import React, { useEffect, useState } from 'react';
import Avatar from '@mui/material/Avatar';
import Chip from '@mui/material/Chip';
import "./chat.css"

import PerfectScrollbar from 'react-perfect-scrollbar';
import { useHistory, useParams } from 'react-router-dom';
import axios from 'axios';

const ChatList = ({showfullchat, getMessageId,getChatFromLocalStorage}) => {
    const history = useHistory() 
    const params = useParams()
    const[designer_img, setDesigner_imgs] = useState()
    const[loader, setLoader] = useState(false)
    const[users, setUsers] = useState()
    const[user, setUser] = useState()
    const[liks, setLiks] = useState([])
    var[community, setCommunity] = useState(0)
    const[currChat, setCurrChat] = useState()
    const [mainf, setMainf] = useState(false)
 
    const getUserProfileInfo=async()=>{
        setLoader(true)
        await axios.get(`/userProfile`)
        .then((res) => {
            if(res.status===200){
                console.log(res.data)
                setUser(res.data)
            }
            setLoader(false)
        }).catch((err) => {
            setLoader(false)
            console.log(err)
            history.push("/login")
        })
    }

    const getUserAllProfileInfo=async()=>{
        setLoader(true)
        setMainf(false)
        await axios.get(`/getAllUsersProfiles`)
        .then((res) => {
            if(res.status===200){
                console.log(res.data)
                setUsers(res.data)
            }
            setLoader(false)
        }).catch((err) => {
            setLoader(false)
            console.log(err)
            history.push("/login")
        })
    }

    const getVipFolder=async()=>{
        setLoader(true)
        setMainf(true)
        await axios.get(`/getAllVIPUsersProfiles`)
        .then((res) => {
            if(res.status===200){
                console.log(res.data)
                setUsers(res.data)
            }
            setLoader(false)
        }).catch((err) => {
            setLoader(false)
            console.log(err)
            history.push("/login")
        })
    }

    const showChat = async(e) => {
        const _id = e.target.id;
        if(!_id){
            return
        }
        console.log(_id)
        try {
            const result = await fetch("/getSingleChat", {
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify({_id})
            })
            const data = await result.json()
            setCurrChat(data)
            getMessageId(_id)
            localStorage.setItem('currChat', JSON.stringify(data))
            getChatFromLocalStorage()
        } catch (error) {
            console.error(error)
        }
    }

    const removeFromVip = async(_id) => {
        console.log(_id)
        console.log("clicked")
        try {
            const res = await fetch("/removeFromVip", {
                method: "POST",
                headers: {
                    "content-type":"application/json"
                },
                body: JSON.stringify({_id})
            })
            const data = await res.json();
            console.log(data)
            getUserAllProfileInfo()
            getVipFolder()
        } catch (error) {
            console.error(error)
        }
    }

    const moveToVip = async(_id) => {
        // const _id = e.target.id
        console.log(_id)
        console.log("clicked")
        try {
            const res = await fetch("/moveToVip", {
                method: "POST",
                headers: {
                    "content-type":"application/json"
                },
                body: JSON.stringify({_id})
            })
            const data = await res.json();
            console.log(data)
            getUserAllProfileInfo()
            getVipFolder()
        } catch (error) {
            console.error(error)
        }
    }

    const getAllVIPUsersProfiles = async(e) => {

        try {
            const res = await fetch("/getAllVIPUsersProfiles", {
                method: "GET",
                headers: {
                    "content-type":"application/json"
                }
            })
            const data = await res.json();
            if(data.status===200){
                setUser(data)
            }
            console.log("data", data)
        } catch (error) {
            console.error(error)
        }
    }

    useEffect(() => {
        getUserAllProfileInfo()
        getUserProfileInfo()
        getAllVIPUsersProfiles()
    }, [])

    return (
        <div className="col-12 px-1">
            <div className="border bg-white py-2 px-0">
                <div className="px-2 h30 pt-1">

                    <div className='d-flex justify-content-between align-items-center px-5'>
                        <div className='ls-1' onClick={getUserAllProfileInfo} style={{ cursor: 'pointer', borderBottom: mainf?'': '3px solid #494848' }}>
                            Main Folder
                        </div>
                        <div style={{ width: '1px', height: '25px', backgroundColor: "#494848" }}>

                        </div>
                        <div className='ls-1' onClick={getVipFolder} style={{ cursor: "pointer", borderBottom: mainf?'3px solid #494848': '' }}>
                            VIP Folder
                        </div>
                    </div>
                    <div className='mt-2 mb-1'>
                        <input type="search" placeholder='Search' className='form-control' />
                    </div>
                </div>
              
              <div className='h60 mt-2'> 
                    <div className="user-chatlist pt-1">

                        <PerfectScrollbar>
                            {users&&users.length>0&&users.map(user => (
                                <>
                                <div 
                                    onClick={showChat} style={{cursor: 'pointer'}} 
                                    id={user._id}
                                    className="main-message"
                                    tabIndex="1"
                                >
                                    <div className="container" id={user._id}>
                                        <div className="d-flex py2" id={user._id}>
                                            <div id={user._id}>
                                                <Avatar
                                                    id={user._id}
                                                    alt="Remy Sharp"
                                                    src={user&&user.file?`/images/${user.file}`:"/static/images/avatar/1.jpg"}
                                                    sx={{ width: 56, height: 56 }}
                                                />
                                            </div>
                                            <div className='ms-3 w-100' id={user._id}>
                                                <div className='d-flex justify-content-between' id={user._id}>
                                                    <p className='mb-0 fw-bold ls-1' id={user._id}>{user&&user.name}</p>
                                                    {user&&user.designer==="vipFolder"?
                                                    <Chip label="Remove from VIP" id={user._id} onClick={()=>removeFromVip(user._id)} variant="outlined" className='ls-1 px-2' size='small' />
                                                    :
                                                    <Chip label="move to VIP" id={user._id} onClick={()=>moveToVip(user._id)} variant="outlined" className='ls-1 px-2' size='small' />}
                                                </div>
                                                <p className="mb-0 ls-1" id={user._id}>click to see chat</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                </>
                            ))}
                        </PerfectScrollbar>


                    </div>
                </div>
             <div  className='container'>
                <p className='text-center mb-0 small ls-1 mt0'>Go to settings for VIP rules</p>
             </div>

            </div>

        </div>
    )
}

export default ChatList