import React from 'react'
import down from './downn.png'
import downn from './down.jpg'
import arow from './arows.png'
const CADPreview = () => {
    const withupdatepreviw=()=>{
        document.querySelector('.previmginner').innerHTML=` <img src='' alt='previw image' id='cadpreviewimg'/>`;
        document.querySelector('#cadpreviewimg').src=downn;
        if(document.querySelector('#pendulumimg').classList.contains('rightpend')){
            document.querySelector('#pendulumimg').classList.remove('rightpend');  
            document.querySelector('#pendulumimg').classList.add('leftpend');
           } else{
            document.querySelector('#pendulumimg').classList.add('leftpend');
           }
 
    }
    const withoutupdatepreviw=()=>{
        document.querySelector('#cadpreviewimg').remove();
       if(document.querySelector('#pendulumimg').classList.contains('leftpend')){
        document.querySelector('#pendulumimg').classList.remove('leftpend');  
        document.querySelector('#pendulumimg').classList.add('rightpend');
       } else{
        document.querySelector('#pendulumimg').classList.add('rightpend');
       }
    }
  return (
    <div>
        <div className='cadpreview'>
            <div className='previwimg'>
                <div className='previmg'>
                    <h3>Preview of the selected Image</h3>
                    <div className='previmginner'>
                    <img src={downn} alt='previw image' id='cadpreviewimg'/>
                    </div>
                </div>
                <div className='cadinput'>
                       <select>
                               <option>Search Car or AFV</option>
                        </select>
                        <span className='caddelete' id='del3'><i className='fas fa-trash-alt'></i></span>
                </div>
            </div>
            <div className='prevbutn1'>
                <button onClick={withupdatepreviw}>Go further with selected vehicle <hr/><span>click here</span></button>
            </div>
            <div className='pendulummain'>
                <div className='pendulum'>
                    {/* <div className='circle'>

                    </div> */}
                    <img src={arow} alt='arrow' id='pendulumimg' className='leftpend'/>
                </div>
            </div>
            <div className='prevbutn1'>
                <button onClick={withoutupdatepreviw}>Continue without specific vehicle <hr/> <span>click here</span></button>
            </div>
        </div>
    </div>
  )
}

export default CADPreview