import React,{useMemo, useState} from 'react'

const CadQuest = ({getQuestions}) => {
    const[questions, setQuestions] = useState({
        chargeable: "",
        free: "",
        request: "",
        money: ""
      })
    
      useMemo(() => {
        getQuestions(questions)
      }, [questions])
    
      const handleChange = (e) => {
        const value = e.target.value;
        const name = e.target.name;
    
        setQuestions({
            ...questions,
            [name]: value
        })
      }

    return (
        <div className='Questing'>
            <div className='inquest'>
                <h3>These question are for our information and will not be published</h3>
                <div className='questing1'>
                    <p>Should the download be chargeable?</p>
                    <form>
                        <input type="radio" id="yes" name="chargeable" onChange={handleChange} value="yes" checked/>
                        <label for="html">Yes</label>
                            <input type="radio" id="no" name="chargeable" onChange={handleChange} value="no" />
                            <label for="css">No</label>
                    </form>
                </div>
                <div className='questing1'>
                    <p>Should the download be free of charge?</p>
                    <form>
                        <input type="radio" id="yes" name="free" onChange={handleChange} value="yes" checked />
                        <label for="html">Yes</label>
                            <input type="radio" id="no" name="free" onChange={handleChange} value="no" />
                            <label for="css">No</label>
                    </form>
                </div>
            
                <div className='questing1'>
                    <p>Downloadable only on personal request</p>
                    <form>
                        <input type="radio" id="yes" name="request" onChange={handleChange} value="yes" checked/>
                        <label for="html">Yes</label>
                            <input type="radio" id="no" name="request" onChange={handleChange} value="no" />
                            <label for="css">No</label>
                    </form>
                </div>
                <div className='questing1'>
                    <p>How much money do you want for the download?</p>
                    <form className='form2'>
                    <input type="text" name='money' onChange={handleChange} placeholder='' />
                    </form>
                </div>
                <div className='questing1'>
                <p>(You will recive a message and can negotiate conditions by yourself)</p>
                    
                </div>
            </div>
          </div>
    )
}

export default CadQuest