import React,{useEffect, useState} from 'react'
import Header from '../header/Header'
import car from '../img/part.png'
import ReactTooltip from 'react-tooltip';
import comunity from '../img/comunity.png'
import insta from '../img/insta.png'
import jury from '../img/jury.png'
import corp from '../img/corp.png'
import Popup from 'reactjs-popup';
import './part.css'
import m1 from '../img/mobile1.png'
import m2 from '../img/mobile2.png'
import m3 from '../img/mobile3.png'
import Footer from '../footer/Footer'
import Partdesign from './singledesign/SingleDesign'
import { connect } from 'react-redux';
import { fetchdesigndata } from '../../redux/designgallery/DesignActions';
import PartComponent from './PartComponent';
import { fetchpartdata } from '../../redux/partgallery/PartActions';
import axios from 'axios';
import { Spinner } from 'react-bootstrap';

const PartG = (props) => {
    const [gridchange,setgridchange]=useState(0);
    const[user, setUser] = useState()

    const singleview=()=>{
         setgridchange(1);
    }
    const doubleview=()=>{
        setgridchange(2);
    }
    const tripleview=()=>{
        setgridchange(3);
    }

    const showsorting=()=>{
        let sortarr=document.querySelectorAll('.partsort');

        for(let i=0;i<3;i++){
            if(sortarr[i].style.display=='none'){
                sortarr[i].style.display='block'; 
            }
            else{
                sortarr[i].style.display='none';
            }
         
        }
        console.log(sortarr)
    }

    const getUserPartImages=async()=>{
        try {
            const res = await fetch("/getAllPartImages", {
                method: "GET",
                headers: {
                    "content-type":"application/json"
                }
            })
            const data = await res.json()
            setUser(data)
            console.log(data)
        } catch (error) {
            console.error(error.message)
        }
    }

    const handleSearch = async(e) => {
        const val  =e.target.value
        console.log(val)
        try {
            const res = await fetch("/searchPartImages", {
                method: "POST",
                headers: {
                    "Content-type": "application/json"
                },
                body: JSON.stringify({val})
            })
            const data = await res.json();
            setUser(data)
        } catch (error) {
            console.error(error)
        }
    }

    useEffect(() => {
        getUserPartImages()
    },[])

  return (
    <div>
        <Header />
        <div className='partgallery'>
         <div className='designheading'>
             <h1>Part Gallery</h1>
         </div>
         <div className='mobilelayouts'>
             <div className='mobilesorting'>
                 <h3 onClick={showsorting}>
                    <svg width="24" height="16" viewBox="0 0 24 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M0 16V14H8V16H0ZM0 2V0H24V2H0ZM0 9V7H16V9H0Z" fill="#4F4F4F"/>
                    </svg>
                    Sort by
                  </h3> 
             </div>
             <div className='difflayouts'>
                 <div className='layimg'>
                    <img src={m1} alt='mobile' onClick={singleview}/>
                 </div>
                 <div className='layimg'>
                   <img src={m2} alt='mobile' onClick={doubleview}/>
                 </div>
                 <div className='layimg'>
                    <img src={m3} alt='mobile' onClick={tripleview}/>
                 </div>
             </div>
         </div>
         <div className='partsort'>
             <form>
                <label>Search</label>
                <input type='text' name='search' onChange={handleSearch} placeholder='Search on this website' className='pladjust'/>
             </form>
         </div>
         <div className='partsort'>
             <form>
                <label>Sort By:</label>
                <input type='text' name='search' placeholder='Standard Sorting' className='pladjust2'/>
                <input type='text' name='search' placeholder='Sort by project' className='pil-3'/>
             </form>
         </div>
         <div className='partsort'>
             <form>
                <label>Category</label>
                <input type='text' name='search' placeholder='main category'/>
                <input type='text' name='search' placeholder='Second category' className='pil-3' />
                <input type='text' name='search' placeholder='Third category' className='pil-3' />
                <input type='text' name='search' placeholder='Sub category' className='pil-3' />
             </form>
         </div>
         <div className={'mainpartgallery' + gridchange}>
              {user?user.map((image) => (
                <PartComponent key={image._id} image={image} getUserPartImages={getUserPartImages} />
              )):
              "No Images..."
              }
         </div>
         {/* <div className={'mainpartgallery' + gridchange}>
            {props.partdata.map(part=>(
                <PartComponent key={part.id} part={part} />
            ))}
         </div> */}
        </div>
        <Footer />
    </div>
  )
}

const mapStateToProps=(state)=>{
    return{
        partdata:state.part.partdata
    }
}
const mapDispatchToProps=dispatch=>{
    return{
        fetchpartdata:()=>{
            dispatch(fetchpartdata());
        }
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(PartG)