import React, { useEffect, useState } from 'react'
import { Spinner } from 'react-bootstrap'
import { useParams } from 'react-router-dom'
import Editor from '../editor/Editor'
import Question from '../part-upload/Question'
import UploadS from '../part-upload/UploadS'
import UploadSect from '../part-upload/uploadSect'


const config = {
  buttons: ["bold", "ol", "ul"],
};
const UploadFormmanu = () => {
    const params = useParams()
    const[text, setText] = useState('')
    const[images, setImages] = useState()
    const[ques, setQues] = useState({})
    const[categ, setCateg] = useState()
    const[loader, setLoader] = useState(false)
    const[loaderS, setLoaderS] = useState(false)
    const[companyId, setCompanyId] = useState()
    const[users, setUsers] = useState();

  const getAllManuUsers = async() => {
    setLoaderS(true)
    try {
        const res = await fetch("/getAllManufacturerUsers", {
            method: "GET",
            headers: {
                "Content-type": "application/json",
            }
        })
        const data = await res.json()
        if(res.status === 200){
            setUsers(data)
            console.log(data)
        }
        setLoaderS(false)
    } catch (error) {
        setLoaderS(false)
        console.log(error)
    }
}

    const getTextData = (data) => {
      setText(data)
    }
    const getFileData = (data) => {
      setImages(data)
    }
    const getQuestions = (data) => {
      setQues(data)
    }
    const getCategory = (data) => {
      setCateg(data)
    }
  
    const addPartManuImages = async() => {
      const user_id = params.id
      console.log(images)
      console.log(text)
      console.log(ques)
      console.log(categ)
      setLoader(true)
      try {
        const res = await fetch("/addPartManuImages",{
          method: "POST",
          headers: {
            "content-type": "application/json"
          },
          body: JSON.stringify({
            part_id: companyId,
            part_imgs: images,
            description: text,
            custom: ques.custom,
            pices: ques.pices,
            proposal: ques.proposal,
            production: ques.production,
            main_categ: categ.main_categ,
            sec_categ: categ.sec_categ,
            third_categ: categ.third_categ,
            sub_categ: categ.sub_categ
          })
        })
        const data = await res.json()
        if(res.status === 200){
            console.log("images uploaded")
          setLoader(false)
        }else{
          setLoader(false)
          alert("Error Occured, please try again")
        }
        setLoader(false)
      } catch (error) {
        setLoader(false)
      }
    }
  
    useEffect(() => {
        getAllManuUsers()
    }, [])

  return (
    <div  className='container mt-5'>
         <form className='mb-5'>
                      <select onChange={(e) => setCompanyId(e.target.value)}>
                          <option>Select  The Company</option>
                          {users&&users.map(user => (
                            <>
                                <option value={user._id}>{user.company}</option>
                            </>
                            ))}
                      </select>
         </form>
        <UploadSect getFileData={getFileData} />
        <UploadS getCategory={getCategory} />
        <Question getQuestions={getQuestions}/>
        <Editor getTextData={getTextData} config={config}  />

        <button onClick={addPartManuImages} id='btn'>
            {loader?<>
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
            </>: "Submit"}
        </button>
    </div>
  )
}

export default UploadFormmanu