import React, { useEffect, useRef, useState } from 'react';
import './editor.css'
import JoditEditor from 'jodit-react';

const Editor = ({config, getTextData}) => {
  const[content, setContent] = useState('')
  const editor = useRef(null)
  let textData = content.replace(/(<([^>]+)>)/gi, "");
  
  useEffect(() => {
    getTextData(textData)
  }, [content])

    return (
      <>
        <div className="editor">
            <div className="inner">
            <span className='shift'>Standard</span>
            <span className='shift'>HTML</span>

            <JoditEditor 
                ref={editor}
                value={content}
                config={config}
                tabIndex={1}
                onBlur={newContent => setContent(newContent)}
                onChange={newContent => {}} 
              />

            </div>
        </div>
      </>
  )
};

export default Editor;
