import React, {useState} from 'react';
import './design.css'
import download from '../img/download.jpg';
import bugati1 from '../img/Bugatti 1.jpg';
import bugati2 from '../img/Bugatti 2.jpg';
import bugati3 from '../img/Bugatti 3.jpg';
import bugati4 from '../img/Bugatti 4.jpg';
import Header from '../header/Header';
import Editor from '../editor/Editor';
import Footer from '../footer/Footer';
import DesignUp from './DesignUp';
import axios from 'axios';
import { useHistory, useParams } from 'react-router-dom';
import { Spinner } from 'react-bootstrap';
const config = {
    buttons: ["bold", "ol", "ul"],
  };

const DesignUpload = () => {
  const history = useHistory()
  const params = useParams()
  const[loader, setLoader] = useState(false)
  const[images, setImages] = useState()
  const[text, setText] = useState('')

  const getTextData = (data) => {
    setText(data)
    console.log(data)
  }
  const getFileData = (data) => {
    setImages(data)
  }

  const addDesignerImages = async() => {
    setLoader(true)
    const user_id = params.id
    try {
      const res = await fetch("/addDesignerImages",{
        method: "POST",
        headers: {
          "content-type": "application/json"
        },
        body: JSON.stringify({
          designer_imgs: images,
          description: text
        })
      })
      const data = await res.json()
      console.log(data)
      if(res.status === 200){
        setImages([])
        history.push("/profile/"+user_id)

      }
      setLoader(false)
    } catch (error) {
      setLoader(false)
    }
  }

  return (
      <div >
        <Header />
            <div className="gallery_main">
                <DesignUp getFileData={getFileData} />
                <Editor getTextData={getTextData} config={config} />
            </div>
            <div className="btn_div">
                <button onClick={addDesignerImages} id='btn'>
                 {loader?<>
                      <Spinner animation="border" role="status">
                          <span className="visually-hidden">Loading...</span>
                      </Spinner>
                  </>: "Submit"}
                </button>
            </div>
        <Footer/>
      </div>
  )
};

export default DesignUpload;
