import React, { useEffect, useState } from 'react';
import './chat.css'
import IconButton from '@mui/material/IconButton';
import { BsEmojiSmile, BsHeart } from 'react-icons/bs';
import { GrAttachment } from 'react-icons/gr';
import { FiSend } from 'react-icons/fi';

import PerfectScrollbar from 'react-perfect-scrollbar';
import { useHistory, useParams } from 'react-router-dom';
import axios from 'axios';

const Chats = ({backtolist, sendMessage, currChat}) => {
    const history = useHistory() 
    const params = useParams()
    const[message, setMessage] = useState()
    const[designer_img, setDesigner_imgs] = useState()
    const[loader, setLoader] = useState(false)
    const[users, setUsers] = useState()
    const[user, setUser] = useState()
    const[liks, setLiks] = useState([])
    var[community, setCommunity] = useState(0)
    const[otherUser, setOtherUser] = useState()
    let[currentChat, setCurrentChat] = useState()
    const[currId, setCurrId] = useState()

    const getUserProfileInfo=async()=>{
        setLoader(true)
        await axios.get(`/userProfile`)
        .then((res) => {
            if(res.status===200){
                console.log(res.data)
                setUser(res.data)
                setCurrId(res.data._id)
            }
            setLoader(false)
        }).catch((err) => {
            setLoader(false)
            console.log(err)
            history.push("/login")
        })
    }

    const sendMessageNow = () => {
        sendMessage(message)
        const data = JSON.parse(localStorage.getItem('currChat'))
        setCurrentChat(data)
        setMessage("")
    }

    const getFromLocalStorage = () => {
        const data = JSON.parse(localStorage.getItem('currChat'))
        setCurrentChat(data)
    }

    const getOtherChatUser = async() => {
        try {
            const data_id = JSON.parse(localStorage.getItem('currChat'))
            const _id = data_id.userSec_id
            console.log(_id)
            const result = await fetch(`/getOtherChatUser`, {
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify({_id})
            })
            const data = await result.json()
            setOtherUser(data)  
            console.log("Other user") 
            console.log(data) 
        } catch (error) {
            console.error(error)
        }
    }
    
    useEffect(async() => {
        setCurrentChat(currChat)
        getFromLocalStorage()
        getUserProfileInfo()
        getOtherChatUser()
    }, [currChat])

    return (
        <div className='col-12'>
            <div className='bactolist'>
               <h5 onClick={()=>backtolist()}> <svg width="10" height="18" viewBox="0 0 10 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M9.79922 3.0001L3.79922 9.0001L9.79922 15.0001L8.59922 17.4001L0.199219 9.0001L8.59922 0.600098L9.79922 3.0001Z" fill="#333333"/>
                </svg>
                Back to all chats</h5>
            </div> 
            <div className="col-12 
            bg-white border p-0 position-relative overflow-hidden">
                <div className='d-flex border-bottom'>
                    {/* <div className='flex-fill border text-center py-2 ls-1' style={{ backgroundColor: '#AC828238' }}>

                    </div> */}
                    <div className='flex-fill border text-center py-2 ls-1 bg-grey'>
                    {otherUser&&otherUser.name} {otherUser&&otherUser.sur_name}
                    </div>
                </div>

                <div className="container-fluid p0" id="message-container">
                    <div className="messages-list">
                        <PerfectScrollbar>

                            <ul className='list-unstyled'>
                                {currentChat&&currentChat.messages?currentChat.messages.map((message) => (
                                    <>
                                    {console.log("currentChat", currentChat)}
                                    <div className={`messagedev`} key={message._id}>
                                        <li 
                                            className={`message-box ls-1 ${message.user_id===currId?'left_msg':'right_msg'}`}
                                        >
                                            {message.message}
                                        </li>
                                    </div>
                                    </>
                                )): "No Chat"}
                            
                            </ul>
                        </PerfectScrollbar>
                    </div>
                </div>

                <div className='message-bottom-bar'>
                    <div className="d-flex">
                        <div className="mx-1">
                            <IconButton aria-label="emoji" size='small'>
                                <BsEmojiSmile />
                            </IconButton>
                        </div>
                        <div className="mx-1">
                            <IconButton aria-label="emoji" size='small'>
                                <BsHeart />
                            </IconButton>
                        </div>
                        <div className="mx-1">
                            <IconButton aria-label="emoji" size='small'>
                                <GrAttachment />
                            </IconButton>
                        </div>
                    </div>
                    <div className='flex-fill position-relative msgput'>
                        <input type="text" value={message} onChange={(e) => setMessage(e.target.value)} className='w-100 ps-3 pe-5' />
                        <IconButton aria-label="emoji" size='small' className='btn-send' id='btnsemd'>
                            <FiSend id={user&&user._id} onClick={sendMessageNow} />
                        </IconButton>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Chats