import React, { useEffect, useState } from 'react'
import DashHeader from './DashHeader'
import Sidebar from './Sidebar';
import './dashboard.css'
const UserS = () => {
    const[loaders, setLoaderS] = useState(false);
    const[users, setUsers] = useState();

    const getAllUsers = async() => {
        setLoaderS(true)
        try {
            const res = await fetch("/getAllUsers", {
                method: "GET",
                headers: {
                    "Content-type": "application/json",
                }
            })
            const data = await res.json()
            if(res.status === 200){
                setUsers(data)
            }
            setLoaderS(false)
        } catch (error) {
            setLoaderS(false)
            console.log(error)
        }
    }

    useEffect(() => {
        getAllUsers()
    }, [])
    
    const approveUser = async(e) => {
        const id = e.target.id
        try {
            try {
                const res = await fetch("/approve_user", {
                    method: "POST",
                    headers: {
                        "Content-type": "application/json",
                    },
                    body: JSON.stringify({id})
                })
                const data = await res.json()
                if(res.status === 200){
                    console.log("approved")
                    getAllUsers()
                }else{
                    console.log(data)
                }
                setLoaderS(false)
            } catch (error) {
                setLoaderS(false)
                console.log(error)
            }
        } catch (error) {
            
        }
    }

  return (
    <div>
        <DashHeader/>
        <div class="container-fluid">
         <div class="row">
             <div class="col-md-2">
               <Sidebar/>
             </div>
             <div class="col-md-10 d-flex custom-flex-gap">
             <div class="container">
                <div class="row">
                    {
                        users&&users.map(user=>(
                            <div class="col-md-10 mx-auto mb-3">
                            <div class="people-nearby shadow px-3">
                  
                                <div class="nearby-user">
                                    <div class="row">
                                    <div class="col-md-2 col-sm-2">
                                        <img src={user&&user.file?`/images/${user.file}`:"/images/avt.jpg"} alt="user" class="profile-photo-lg"/>
                                    </div>
                                    <div class="col-md-7 col-sm-7 d-flex align-items-center justify-content-around">
                                        <h5><a href="#" class="profile-link">{user&&user.name} {user&&user.sur_name}</a></h5>
                                        <p className='px-3'>{user&&user.func}</p>
                                    </div>
                                    <div class="col-md-3 col-sm-3 d-flex align-items-center justify-content-center">
                                        <button class="btn btn-primary pull-right" id={user&&user._id}
                                            disabled={user&&user.isApproved?true:false}
                                            onClick = {approveUser}
                                        >{user&&user.isApproved?"Approved":"Approve"}</button>
                                    </div>
                                    </div>
                                </div>
                                
                            </div>
                        </div>  
                        ))
                    }
                </div>
             </div>
         </div>
     </div>
    </div>
    </div>
  )
}

export default UserS