import React, { useState } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import Editor from '../editor/Editor'
import Footer from '../footer/Footer'
import Header from '../header/Header'
import UploadS from '../part-upload/UploadS'
import './cad.css'
import CadCat from './CadCat'
import CADPreview from './CADPreview'
import CadQuest from './CadQuest'
import CadUpart from './CadUpart';
const config = {
  buttons: ["bold", "ol", "ul"],
};
const CadUploads = () => {
  const history = useHistory()
  const params = useParams()
  const[images, setImages] = useState([])
  const[text, setText] = useState()
  const[categ, setCateg] = useState()
  const[ques, setQues] = useState()
  const[loader, setLoader] = useState(false)

  const getTextData = (data) => {
    setText(data)
  }
  const getFileData = (data) => {
    setImages(data)
  }
  const getCategory = (data) => {
    setCateg(data)
  }
  const getQuestions = (data) => {
    setQues(data)
  }

  const submitCadData = async() => {
    console.log(images, text, categ, ques)
    const user_id = params.id
    console.log(images)
    console.log(text)
    console.log(ques)
    console.log(categ)
    setLoader(true)
    try {
      const res = await fetch("/addCadImages",{
        method: "POST",
        headers: {
          "content-type": "application/json"
        },
        body: JSON.stringify({
          cad_imgs: images,
          description: text,
          custom: ques.custom,
          pices: ques.pices,
          proposal: ques.proposal,
          production: ques.production,
          main_categ: categ.main_categ,
          sec_categ: categ.sec_categ,
          third_categ: categ.third_categ,
          sub_categ: categ.sub_categ
        })
      })
      const data = await res.json()
      if(res.status === 200){
        console.log(data)
        setLoader(false)
        history.push(`/profile/${data._id}`)  
      }else{
        setLoader(false)
        alert("Error Occured, please try again")
      }
      setLoader(false)
    } catch (error) {
      setLoader(false)
    }
  }

  return (
    <div>
      <Header/>
    <div className='cadup'>
      <CADPreview />
      <CadUpart getFileData={getFileData} />
      <CadCat getCategory={getCategory} />
      <CadQuest getQuestions={getQuestions} />
      <Editor getTextData={getTextData} config={config} />
      <button onClick={submitCadData}>Submit</button>
      <Footer />
    </div>
    </div>
  )
}

export default CadUploads