import React,{useState} from 'react'
import { Link } from 'react-router-dom'
import './dashboard.css'
import DashHeader from './DashHeader'
import Sidebar from './Sidebar'

const UserRegister = () => {
    const [user,setUser]=useState({
        surname:'',
        title:'',
        fname:'',
        email:'',
        password:'',
        password2:''
    });
  const {surname,title, fname,email,password,password2}=user;
    const onchange=e=> {
        setUser({...user,[e.target.name]:e.target.value})
    }

    const onSubmit=e=>{
        console.log(user)
        e.preventDefault();
        if(password !== password2){
            alert('password  do not match','danger');
        }
        else{
            alert('registerb User');
        }
       
    }
  return (
    <div>
        <DashHeader/>
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-md-2'>
                    <Sidebar/>
                </div>
                <div className='col-md-10'>
                    <h1 className='adduser mt-5'>ADD USER</h1>
                    <form onSubmit={onSubmit}>
                        <div className='gender'>
                        <input type='radio' name='gender' id='male'/>
                        <label>Male</label>
                        <input type='radio' name='gender' id='female'/>
                        <label>Female</label>
                        </div>
                        <div className='register-fields'>
                            <div className='fields1'>
                                
                                <label htmlFor="title">Title</label>
                                <br />
                                <input type="text" id='title' name='title' onChange={onchange} required />
                                <br />
                                <label htmlFor="fname">First Name</label>
                                <br />
                                <input type="text" id='fname' name='fname' onChange={onchange} required/>
                                <br />
                                <label htmlFor="surname">Surname</label>
                                <br />
                                <input type="text" id='surname' name='surname' onChange={onchange} required/>
                            </div>
                            <div className='fields1'>
                            
                                <label htmlFor="email">Email</label>
                                <br />
                                <input type="email" id='email' name='email' onChange={onchange} required/>
                                <br />
                                <label htmlFor="password">Password</label>
                                <br />
                                <input type="password" id='password' name='password' onChange={onchange} required/>
                                <br />
                                <label htmlFor="password2">Comfirm Password</label>
                                <br />
                                <input type="password" id='confirmpassword' name='password2' onChange={onchange} required/>
                            </div>
                        </div>
                        <div className='signupbtn'>
                            <input type='submit' value='Submit' className='submit' />
                        </div>
                    </form>
                </div>
            </div>
        </div>
        
    </div>
  )
}

export default UserRegister