import React, { useEffect, useState } from 'react'
import DashHeader from './DashHeader'
import './dashboard.css';
import { Link } from 'react-router-dom'
import Sidebar from './Sidebar';
const Dashboard = () => {
    const[loaders, setLoaderS] = useState(false);
    const[users, setUsers] = useState();
    const[designers, setDesigners] = useState();
    const[manufacturers, setManufacturers] = useState();

    const getAllUsers = async() => {
        setLoaderS(true)
        try {
            const res = await fetch("/getAllUsers", {
                method: "GET",
                headers: {
                    "Content-type": "application/json",
                }
            })
            const data = await res.json()
            if(res.status === 200){
                setUsers(data)
            }
            setLoaderS(false)
        } catch (error) {
            setLoaderS(false)
            console.log(error)
        }
    }

    const getAllDesignerImages = async() => {
        setLoaderS(true)
        try {
            const res = await fetch("/getAllDesignerImagesForDashboard", {
                method: "GET",
                headers: {
                    "Content-type": "application/json",
                }
            })
            const data = await res.json()
            if(res.status === 200){
                console.log(data.length)
                setDesigners(data)
            }
            setLoaderS(false)
        } catch (error) {
            setLoaderS(false)
            console.log(error)
        }
    }

    const getAllManufacturersImages = async() => {
        setLoaderS(true)
        try {
            const res = await fetch("/getAllManufacturersImages", {
                method: "GET",
                headers: {
                    "Content-type": "application/json",
                }
            })
            const data = await res.json()
            if(res.status === 200){
                console.log(data)
                setManufacturers(data)
            }
            setLoaderS(false)
        } catch (error) {
            setLoaderS(false)
            console.log(error)
        }
    }

    useEffect(() => {
        getAllUsers()
        getAllDesignerImages()
        getAllManufacturersImages()
    }, [])

  return (
    <div>
        <DashHeader/>
         <div class="container-fluid">
         <div class="row">
             <div class="col-md-2">
               <Sidebar/>
             </div>
             <div class="col-md-10 d-flex custom-flex-gap">
              <Link to='/allusers/adminId' className='myusercardlink'>
                   <div className='shadow d-flex myusercard'>
                       <div className='lefticons'>
                        <svg xmlns="http://www.w3.org/2000/svg" width="26" height="66" fill="currentColor" class="bi bi-person" viewBox="0 0 26 26">
                            <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"/>
                        </svg>
                       </div>
                       <div className='textside'>
                           <h2> Total Users</h2>
                           <p>{users&&users.length}</p>
                       </div>
                   
                   </div>
                 </Link>
                 <Link to='/pending-request/adminId' className='myusercardlink'>  
                   <div className='shadow d-flex myusercard'>
                       <div className='lefticons'>
                        <svg xmlns="http://www.w3.org/2000/svg" width="26" height="66" fill="currentColor" class="bi bi-person" viewBox="0 0 26 26">
                            <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"/>
                        </svg>
                       </div>
                       <div className='textside'>
                           <h2>Add Jury Members</h2>
                           <p>{users&&users.length}</p>
                       </div>
                   
                   </div>
                   </Link>
                   <Link to='/designer-images-approve/adminId' className='myusercardlink'>
                   <div className='shadow d-flex myusercard'>
                       <div className='lefticons'>
                        <svg xmlns="http://www.w3.org/2000/svg" width="26" height="66" fill="currentColor" class="bi bi-person" viewBox="0 0 26 26">
                            <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"/>
                        </svg>
                       </div>
                       <div className='textside'>
                           <h2>Designer Images</h2>
                           <p>{designers&&designers.length}</p>
                       </div>
                   
                   </div>
                   </Link>
                   <Link to='/manufacturer-images-approve/adminId' className='myusercardlink'>
                   <div className='shadow d-flex myusercard'>
                       <div className='lefticons'>
                        <svg xmlns="http://www.w3.org/2000/svg" width="26" height="66" fill="currentColor" class="bi bi-person" viewBox="0 0 26 26">
                            <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"/>
                        </svg>
                       </div>
                       <div className='textside'>
                       <h2>Manufacturer Images</h2>
                       <p>{manufacturers&&manufacturers.length}</p>
                       </div>
                   </div>
                   </Link>

                   <Link to='/add-designer-images/adminId' className='myusercardlink'>
                   <div className='shadow d-flex myusercard'>
                       <div className='lefticons'>
                        <svg xmlns="http://www.w3.org/2000/svg" width="26" height="66" fill="currentColor" class="bi bi-person" viewBox="0 0 26 26">
                            <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"/>
                        </svg>
                       </div>
                       <div className='textside'>
                       <h2>Add Designer Content</h2>
                       </div>
                   </div>
                   </Link>

                   <Link to='/add-manufacturer-images/adminId' className='myusercardlink'>
                   <div className='shadow d-flex myusercard'>
                       <div className='lefticons'>
                        <svg xmlns="http://www.w3.org/2000/svg" width="26" height="66" fill="currentColor" class="bi bi-person" viewBox="0 0 26 26">
                            <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"/>
                        </svg>
                       </div>
                       <div className='textside'>
                       <h2>Add Manufacturer Content</h2>
                       </div>
                   </div>
                   </Link>
             </div>
         </div>
     </div>

    </div>
  )
}

export default Dashboard