import React ,{useState}from 'react'
import './single.css';
import InfoLogo from './img/info.png';
import bookmark from './img/save.png';
import door from './img/door.png';
import share from './img/share.png';
import heartfull from './img/heart.png';
import heartdrop from './img/hdrop.png'
import factory from './img/factory.png';
import comunity from'./img/commiunity.png';
import group from './img/group.png';
import ReactTooltip from "react-tooltip";
import insta from './img/instagram.png';

const SingleDesign = (props) => {
    const part=props.part;
    let {post_image,community_likes,profile_image,insta_likes,factory_likes,group_likes,post_on}= part;
    const showsing=props.show;
    const [comment,setComment]=useState('');
    const onchange=(e)=>{
        setComment(e.target.value)
        e.preventDefault();
    }
    const cumunitydrop=(e)=>{
        // e.target.src={heartdrop};
        const heartchnge= document.querySelector('#cumunitydrop')
        heartchnge.src=heartdrop;
        heartchnge.style.width='20px';
        heartchnge.style.height='20px';
        heartchnge.style.margin='0 5px';
        community_likes=community_likes-1;
        document.querySelector('#comunitylike').innerText=community_likes;
        e.preventDefault();
        console.log('heartdrop')
        console.log(showsing ? 'yes' : 'no')
    }
  return (
    <div className='singledesign'>
       <div className='sdesignimg'>
        
           <img src={post_image} className='img-fluid'/>
       </div>
       <div className='sdesigndetail'>
           <div className='sdesignhead'>
               <img src={profile_image} alt='profile pic' width='45px' height='45px' />
               <h6>Mr.sheep</h6>
           </div>
           <div className='sdescription1'>
               <p>Description</p>
           </div>
           <div className='scomments1'>
              <p>Comments</p>
              <div className='spcomments'>
                    <img src={profile_image} alt='profile pic' width='35px' height='35px' />
                    <div className='authornames'>
                        <h6>Mr.sheep</h6>
                         <p>This is good one</p>
                    </div>   
                </div>
                <div className='spcomments'>
                    <img src={profile_image} alt='profile pic' width='35px' height='35px' />
                    <div className='authornames'>
                        <h6>Mr.sheep</h6>
                         <p>This is good one</p>
                    </div>   
                </div>
                <div className='spcomments'>
                    <img src={profile_image} alt='profile pic' width='35px' height='35px' />
                    <div className='authornames'>
                        <h6>Mr.sheep</h6>
                         <p>This is good one</p>
                    </div>   
                </div>
           </div>
           <div className='commentinput'>
               <form>
                   <input type='text' placeholder='Write your comments' value={comment} onChange={onchange} />
               </form>
           </div>
           <div className='publisheddetails'>
               <div className='pubdate'>
                   <h5>Published on {post_on}</h5>
               </div>
               <div className='pubdetails'>
                <img src={InfoLogo} height='30px' width='30px' />
                <img src={bookmark} height='30px' width='30px' />
                <img src={door} height='35px' width='35px' />
                <img src={share} height='35px' width='35px' />
               </div>
           </div>
           <div className='cfoot'>
                                   <div className='frow'>
                                        <div className='footicongroup' >
                                                         <div>
                                                         <img src={comunity} height='35px' width='34px' data-tip data-for="registerTipo7" data-arrow-color='white' />
                                                            <ReactTooltip id="registerTipo7" place="top"   effect="solid" >
                                                            These are the likes of of our community
                                                                </ReactTooltip>
                                                             </div>
                                                           
                                                                <img src={heartfull} height='30px' width='30px' className='my'  data-tip data-for="registerTipo8" data-arrow-color='white'/>
                                                                <ReactTooltip id="registerTipo8" place="top"   effect="solid" >
                                                            These are the likes of of our community
                                                                </ReactTooltip>
                                                            <span style={{marginTop:"0px", fontSize:"26px"}} data-tip data-for="registerTipo9" data-arrow-color='white'>{part.community_likes}</span> <ReactTooltip id="registerTipo9" place="top"   effect="solid" >
                                                            These are the likes of of our community
                                                                </ReactTooltip>
                                        </div>
                                   
                                   </div>
                                   
                               </div>
       </div>
    </div>
  )
}

export default SingleDesign