import React from 'react'
import AnsReply from './AnsReply'
import Answer from './Answer'

const Gview = ({article}) => {
    console.log("articles", article)
  return (
    <div className='animate__animated animate__fadeIn animate__slow'>
        <div className='questiondisc'>
            
            {article&&article.map((qa) => (
                <>
                    <h6>Question:</h6>
                    <p>{qa.question}</p>
                </>
            ))}
            <div className='discfilter'>
                <div className='tans'>
                    <h6>{article&&article.length} answers</h6>
                </div>
                <div className='tfilter'>
                    <div className='finput'>
                        <input type='search' placeholder='Search' />
                    </div>
                   <div className='finput'>
                       <select>
                           <option>Sort</option>
                       </select>
                   </div>
                   <div className='finput'>
                        <button>Contribute</button>
                    </div>
                </div>
            </div>
            <Answer />
            <div className='replyanswe' id='replyans'>
              <AnsReply/>
              <div className='replyanswe' id='replyans'>
                <AnsReply/>
              </div>
            </div>
             <hr/>
             <Answer />
             <div className='replyanswe' id='replyans'>
              <AnsReply/>
            </div>
            <div className='morereplybtn'>
                <button>+64 more comments</button>
            </div>
            <div className='pagination'>
                <div className='previous'>
                    <svg width="5" height="7" viewBox="0 0 5 7" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M4.243 6.873L0.96 3.59L4.243 0.306999L4.901 0.950999L2.227 3.59L4.901 6.229L4.243 6.873Z" fill="#333333"/>
                    </svg>
                </div>
                <div className='pagenum'>
                    1
                </div>
                <div className='pagenum'>
                    2
                </div>
                <div className='pagenum'>
                    3
                </div>
                <div className='pagenum'>
                    4
                </div>
                <div className='next'>
                    <svg width="5" height="7" viewBox="0 0 5 7" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path d="M1.353 6.873L0.702 6.229L3.369 3.59L0.702 0.950999L1.353 0.306999L4.643 3.59L1.353 6.873Z" fill="#333333"/>
                    </svg>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Gview