import React, { useState } from 'react'
import FileBase64 from 'react-file-base64';
import eagle from '../img/eag.png';
import eag from '../img/emptyimg.png'

const UploadSect = ({getFileData}) => {
    const[loader, setLoader] = useState(false)
  const[images, setImages] = useState([])
  let fileArr = [];
  let fileObj = []

  const handleDone = async({base64}) => {
    setLoader(true)
    fileObj.push(base64)
    
    setImages([...images, base64])
    setLoader(false)
  }
  getFileData(images)

  const removeImage = (e)=> {
    const imge = e.target.id;
    const arr = images.filter(item => item!==imge)
    console.log("filtered",arr)
    console.log(images)
    setImages(arr)
  }

  return (
    <div>
        <div className='uplcenter'>
            <div className='imageupload'>
                <div className='imgup'>
                    <img src={images[0]?images[0]:eagle} alt='image' />
                    <h2>Cover image</h2>
                </div>
                <div className='btnup'>
                    <label htmlFor="inputTag">
                        {/* Upload Image */}
                        <FileBase64
                            multiple={ false }
                            onDone={handleDone} />
                        {/* <input onChange={handleChange} id='inputTag' type="file" /> */}
                    </label>
                </div>
            </div>
            <div className='uploadedimg'>
                {/* <div className='uplsecimg'>
                    <img src={images[0]?images[0]:eagle} alt='img'/>
                    <h6 className='partcoverimg'>Cover image</h6>
                    <span className='partpicdelete'><i className='fas fa-trash-alt'></i></span>
                </div> */}
                {images&&images.map((imgs, index) => (
                    <div className='uplsecimg' key={index} >
                        <img src={imgs} alt='img'/>
                        <span className='partpicdelete'><i id={imgs} onClick={removeImage} className='fas fa-trash-alt'></i></span>
                    </div>
                ))}
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                    {/* <h6>Cover image</h6>
                    <span className='picdelete'><i className='fas fa-trash-alt'></i></span> */}
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                    {/* <h6>Cover image</h6>
                    <span className='picdelete'><i className='fas fa-trash-alt'></i></span> */}
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                  
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                  
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                    
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                 
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>

                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                 
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                </div>
            </div>
        </div>
    </div>
  )
}

export default UploadSect