import React, { useEffect, useState } from 'react'
import DashHeader from './DashHeader'
import Sidebar from './Sidebar';
import './dashboard.css'
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';

const DesignerImages = () => {
    const[loaders, setLoaderS] = useState(false);
    const[designers, setDesigners] = useState();
    const [show, setShow] = useState(false);
    const[likes, setLikes] = useState()
    const[likesId, setLikesId] = useState()

  const handleClose = () => setShow(false);
  const handleShow = (e) => {
    setLikesId(e.target.id)
    setShow(true)
};

    const getAllDesignerImages = async() => {
        setLoaderS(true)
        try {
            const res = await fetch("/getAllDesignerImagesForDashboard", {
                method: "GET",
                headers: {
                    "Content-type": "application/json",
                }
            })
            const data = await res.json()
            if(res.status === 200){
                console.log(data)
                setDesigners(data)
            }
            setLoaderS(false)
        } catch (error) {
            setLoaderS(false)
            console.log(error)
        }
    }

    useEffect(() => {
        getAllDesignerImages()
    }, [])
    
    const approveDesignerImage = async(e) => {
        const id = e.target.id
        try {
            const res = await fetch("/approveDesignerImage", {
                method: "POST",
                headers: {
                    "Content-type": "application/json",
                },
                body: JSON.stringify({id})
            })
            const data = await res.json()
            if(res.status === 200){
                console.log("approved")
                getAllDesignerImages()
            }else{
                console.log(data)
                alert("Not found")
            }
            setLoaderS(false)
        } catch (error) {
            setLoaderS(false)
            console.log(error)
        }
    }

    const addInstaLikes = async(e) => {
        const id = e.target.id
        try {
            const res = await fetch("/addInstaLikes", {
                method: "POST",
                headers: {
                    "Content-type": "application/json",
                },
                body: JSON.stringify({likesId, likes})
            })
            const data = await res.json()
            if(res.status === 200){
                console.log("Insta Likes Added")
                getAllDesignerImages()
                setShow(false)
            }
            setLoaderS(false)
        } catch (error) {
            setLoaderS(false)
            console.log(error)
        }
    }


  return (
    <div>
    <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                    <Form.Label>Instagram Likes</Form.Label>
                    <Form.Control type="email" onChange = {(e)=>setLikes(e.target.value)} />
                </Form.Group>
            </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={addInstaLikes}>
            Add Likes
          </Button>
        </Modal.Footer>
      </Modal>
        <DashHeader/>
        <div class="container-fluid">
         <div class="row">
             <div class="col-md-2">
               <Sidebar/>
             </div>
             <div class="col-md-10 d-flex custom-flex-gap">
             <div class="container">
                <div class="row">
                    {
                        designers&&designers.map(designer=>(
                            <div class="col-md-10 mx-auto mb-3">
                            <div class="people-nearby shadow px-3">
                  
                                <div class="nearby-user">
                                    <div class="row">
                                    <div class="col-md-2 col-sm-2">
                                        <img src={designer&&designer.designer_imgs} alt="user" class="profile-photo-lg"/>
                                    </div>
                                    <div class="col-md-7 col-sm-7 d-flex align-items-center justify-content-around">
                                        <h5><a href="#" class="profile-link">{designer&&designer.designer_name}</a></h5>
                                        <p className='px-3'>{designer&&designer.instaLikes}</p>
                                    </div>
                                    <div class="col-md-3 col-sm-3 d-flex align-items-center justify-content-center">
                                        <button class="btn btn-primary pull-right" id={designer&&designer._id}
                                            disabled={designer&&designer.isApproved?true:false}
                                            onClick = {approveDesignerImage}
                                        >{designer&&designer.isApproved?"Approved":"Approve"}</button>
                                        <Button variant="primary" id={designer._id} onClick={handleShow}>
                                            Add Insta
                                        </Button>
                                    </div>
                                    </div>
                                </div>
                                
                            </div>
                        </div>  
                        ))
                    }
                </div>
             </div>
         </div>
     </div>
    </div>
    </div>
  )
}

export default DesignerImages