import React, {useState} from 'react';
import './partu.css'
import download from '../img/download.jpg';
import eag from '../img/eag.png';
import eagle from '../img/eagle.png';
import Header from '../header/Header';
import UploadS from './UploadS';
import Question from './Question';
import Editor from '../editor/Editor';
import UploadSect from './uploadSect';
import Footer from '../footer/Footer';
import { useHistory, useParams } from 'react-router-dom';
import { Spinner } from 'react-bootstrap';

const config = {
  buttons: ["bold", "ol", "ul"],
};

const PartU = () => {
  const history = useHistory()
  const params = useParams()
  const[text, setText] = useState('')
  const[images, setImages] = useState()
  const[ques, setQues] = useState({})
  const[categ, setCateg] = useState()
  const[loader, setLoader] = useState(false)

  const getTextData = (data) => {
    setText(data)
  }
  const getFileData = (data) => {
    setImages(data)
  }
  const getQuestions = (data) => {
    setQues(data)
  }
  const getCategory = (data) => {
    setCateg(data)
  }

  const addPartImages = async() => {
    const user_id = params.id
    console.log(images)
    console.log(text)
    console.log(ques)
    console.log(categ)
    setLoader(true)
    try {
      const res = await fetch("/addPartImages",{
        method: "POST",
        headers: {
          "content-type": "application/json"
        },
        body: JSON.stringify({
          part_imgs: images,
          description: text,
          custom: ques.custom,
          pices: ques.pices,
          proposal: ques.proposal,
          production: ques.production,
          main_categ: categ.main_categ,
          sec_categ: categ.sec_categ,
          third_categ: categ.third_categ,
          sub_categ: categ.sub_categ
        })
      })
      const data = await res.json()
      if(res.status === 200){
        setLoader(false)
        history.push(`/profile/${data._id}`)  
      }else{
        setLoader(false)
        alert("Error Occured, please try again")
      }
      setLoader(false)
    } catch (error) {
      setLoader(false)
    }
  }

  return (
      <div id='partup'>
      <div className={`loader ${loader?"loader_active":""}`}>
          <Spinner animation="border" role="status">
              <span className="visually-hidden">Loading...</span>
          </Spinner>
      </div>
          <Header />
            <div className="gallery_main">
                <UploadSect getFileData={getFileData} />
                <UploadS getCategory={getCategory} />
                <Question getQuestions={getQuestions} />
                <Editor getTextData={getTextData} config={config} />
            </div>
            <div className="btn_div">
                <button onClick={addPartImages} id='btn'>Submit</button>
            </div>
            <Footer />
      </div>
  )
};

export default PartU;
