import React, {useState} from 'react';
import DesignUp from "../design-upload/DesignUp" 
import { useParams } from 'react-router-dom';
import { Spinner } from 'react-bootstrap';
import Editor from '../editor/Editor';
import { useEffect } from 'react';
const config = {
    buttons: ["bold", "ol", "ul"],
  };

const UploadForm = () => {
  const params = useParams()
  const[loader, setLoader] = useState(false)
  const[images, setImages] = useState()
  const[text, setText] = useState('')
  const[companyId, setCompanyId] = useState()
  const[loaderS, setLoaderS] = useState()
  const[users, setUsers] = useState();

  const getAllDesignerUsers = async() => {
    setLoaderS(true)
    try {
        const res = await fetch("/getAllDesignerUsers", {
            method: "GET",
            headers: {
                "Content-type": "application/json",
            }
        })
        const data = await res.json()
        if(res.status === 200){
            setUsers(data)
            console.log(data)
        }
        setLoaderS(false)
    } catch (error) {
        setLoaderS(false)
        console.log(error)
    }
}

  const getTextData = (data) => {
    setText(data)
    console.log(data)
  }
  const getFileData = (data) => {
    setImages(data)
  }

  const addDesignerImages = async() => {
    setLoader(true)
    try {
      const res = await fetch("/addDesignerAdminImages",{
        method: "POST",
        headers: {
          "content-type": "application/json"
        },
        body: JSON.stringify({
          designer_imgs: images,
          description: text,
          designer_id: companyId
        })
      })
      const data = await res.json()
      console.log(data)
      if(res.status === 200){
        setImages([])
        window.location.reload()
      }
      setLoader(false)
    } catch (error) {
      setLoader(false)
    }
  }

  useEffect(() => {
    getAllDesignerUsers()
  }, [])

  return (
    <div  className='container mt-5'>
      <select onChange={(e) => setCompanyId(e.target.value)}>
        <option value="">Select Company</option>
        {users&&users.map(user => (
          <>
            <option value={user._id}>{user.company}</option>
          </>
        ))}
      </select>

        <div className="gallery_main">
                <DesignUp getFileData={getFileData} />
                <Editor getTextData={getTextData} config={config} />
            </div>
            <div className="btn_div">
                <button onClick={addDesignerImages} id='btn'>
                 {loader?<>
                      <Spinner animation="border" role="status">
                          <span className="visually-hidden">Loading...</span>
                      </Spinner>
                  </>: "Submit"}
                </button>
            </div>
    </div>
  )
}

export default UploadForm