import React, { useState } from 'react'
import Footer from '../footer/Footer';
import Header from '../header/Header'
import ChatList from './ChatList'
import Chats from './Chats';
const Message = () => {
    const [chatshow,setchatshow]=useState(true)
    const [currChat,setCurrChat]=useState()
    const[secId, setSecId] = useState()
  const showfullchats=()=>{
    console.log(false)
     setchatshow(false)
  }
  const backtolists=()=>{
    console.log(true)
    setchatshow(true)
  }

  const getChatFromLocalStorage = () => {
    const data = JSON.parse(localStorage.getItem('currChat'))
    setCurrChat(data)
}

  const sendMessage = async(message) => {
    if(!secId || !message){
      return
    }
    console.log(secId, message)
      try {
          const result = await fetch("/createMessage", {
              method: "POST",
              headers: {
                  "content-type": "application/json"
              },
              body: JSON.stringify({secId, message})
          })
          const data = await result.json()
          setCurrChat(data)
          localStorage.setItem('currChat', JSON.stringify(data))
          getChatFromLocalStorage()
          console.log(data)
      } catch (error) {
          console.error(error)
      }
  }

  const getMessageId = async(id) => {
    console.log("user sec id "+id)
    setSecId(id)
  }

  return (
    <div>
        <Header />
        <div id='chatpage'>
            <div className="container" id='chatcontainer'>
                <div className="rows" id='chatrows'>
                  <div className={chatshow ? 'chatlistusers':'nochatlistusers'}>
                    <ChatList getChatFromLocalStorage={getChatFromLocalStorage} getMessageId={getMessageId} showfullchat={showfullchats}/>
                  </div>
                  <div className={chatshow ? 'chats':'nochats'}>
                    <Chats currChat={currChat} sendMessage={sendMessage} backtolist={backtolists}/>
                  </div>      
                </div>
            </div>
        </div>
        <Footer />
    </div>
  )
}

export default Message