import React from 'react'
import './home.css';
import map from '../img/map.png'
import frame from '../img/bgcon.png'
import quotes from '../img/quotes.png'
import LogoutHeader from '../header/LogoutHeader';
import Footer from '../footer/Footer';
import { Link } from 'react-router-dom';
const Home = () => {
 
  return (
    <div>
        <LogoutHeader />
        <div className='hero'>
          <h6>A new world for automobile manufacturing</h6>
          <h1>EEVE PROJECT <span id='homeheaddash'>-</span> <span id='landindustry'>Transforming The Automotive Industry</span></h1>
          <p>
              Have you ever wished the automotive industry was more flexible and open to new ideas? At EEVE PROJECT, we believe it should be and we are challenging the old ways. 
          </p> 
          <p> 
            We are creating a global community of collaborative manufacturing and design that will revolutionize the automobile industry and enable every potential contributor, whether straight out of college or 30 years in the industry, to play their part. We aim to mobilize the creativity of the world.
          </p>
          <div className='discover'>
              <a href='#'>Discover</a>
              <svg width="34" height="16" viewBox="0 0 34 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M33.7071 8.70711C34.0976 8.31658 34.0976 7.68342 33.7071 7.29289L27.3431 0.928932C26.9526 0.538408 26.3195 0.538408 25.9289 0.928932C25.5384 1.31946 25.5384 1.95262 25.9289 2.34315L31.5858 8L25.9289 13.6569C25.5384 14.0474 25.5384 14.6805 25.9289 15.0711C26.3195 15.4616 26.9526 15.4616 27.3431 15.0711L33.7071 8.70711ZM0 9H33V7H0V9Z" fill="#3E3E3E"/>
              </svg>
              <div className='browncircle'>
                  <div className='circlemenu'>
                    <div className='menufaq'>
                        <Link to='/faq' className='circlelink'>
                          f<br/>a<br/>q<br/>

                        </Link>
                    </div>
                    <div className='menugallery'>
                        <Link to='/designgallery' className='circlelink'>
                           part gallery   
                         
                        </Link> 
                        <Link to='/partgallery' className='circlelink'>
                        design gallery
                        </Link> 
                        <Link to='/cadgallery' className='circlelink'>
                          CAD gallery
                        </Link> 
                    </div>
                  </div>
              </div>
          </div>
        </div>
        {/* map section */}
        <div className='maps'>
            <div className='mapimage'>
               <img src={map} alt='maps' />
            </div>
            <div className='maptextsect'>
              <h6>Collaborative Developing</h6>
              <h2>Engineering without limitations</h2>
              <p>
              Imagine an industrial community that allows automobile designers, suppliers, engineers, university researchers, inventors, developers, and visionaries to join together to originate and build road (or air) vehicles in ways they have never been made before. 
              </p>
            </div>
            <div className='mtextpart'>
              <p>
              Imagine design proposals being evaluated and get developed collaboratively from different corners of the world by partners of every culture, gender, age, and creed. Imagine a future of collaborative manufacturing and creativity. That is what EEVE PROJECT is about.
              </p>
            </div>
        </div>
        {/* map section end */}
        {/* eeve project details section start */}
        <div className='eeveestablish'>
          <div className='eeveouter'>
            <div className='eeveiner'>
              <h6>Collaborative Manufacturing</h6>
              <h2 id='desktopestab'>EEVE PROJECT establishes a melting-pot of ideas to provide solutions</h2>
              <h2 id='mobileestablish'>Engineering without limitations</h2>
              <p>
              Imagine a re-invented automotive industry with a framework for connecting contributors and pooling imagination and expertise to create vehicles that the traditional industry would never consider.
              </p>
              <p>
               Imagine how automobiles can reduce their carbon footprint without necessarily focusing entirely on electric propulsion. Why not think more innovatively about energy sources such as hydrogen, alternative gasoline or technologies currently underdeveloped? 
              </p>
            </div>
          </div>
        </div>
        {/* eeve project details end */}
        {/* testimonial start */}
        <div className='testmonial'>
          <div className='quotes'>
             <img src={quotes} alt='quotes' />
          </div>
          <div className='testmonialtext'>
            <h3>Daniel H. Pink asks the hypothetical question:</h3>
            <p>
            Imagine it is 1995. You sit together with an economist, a university professor, and economics. And someone says I have a magic crystal ball and can see 15 years into the future and would like to test its predictive power. He describes two situations, an existing project and another that will be launched in a few years, and asks them to predict which projects will be more successful in 2010.  
            </p>
            <p>     
              The first project comes from Microsoft; as you know, Microsoft is a big profitable company. Microsoft's project was to create an encyclopedia. It will hire professional writers, editors to create thousands of well-written articles, and well-paid managers to coordinate the whole thing.
            </p>
            <p>
              The second project does not come from a company. A community writes it of 100,000 members who participate for the joy of writing and editing. And no one will be paid for writing and editing. The encyclopedia will exist online, and access will be free with no fees for all to use. You would not have found a single economist anywhere on the planet who predicted that the latter would prevail.
            </p>
            <h6>From Book "Drive what motivate us,"</h6>
          </div>
          <div className='testmonialpara'>
              <p>
                Perhaps EVE PROJECT is not directly comparable to Wikipedia, but the parable shows the power of decentralization of collaborative cooperation; it shows us that forms of society that were unthinkable a few years ago are shaping our future.
              </p>
          </div>
        </div>
        {/* testimonial end */}
        {/* features start */}
        <div className='features'>
          <div className='featuretext'>
            <h6>Automotive Opportunities</h6>
            <h2>Features for automotive designer</h2>
            <p>
            EEVE PROJECT is unique as a vehicle manufacturing community. We are not tied to traditional manufacturers in any way. We believe the worlds population of automobile designers, engineers, and thinkers deserve more opportunities so they can be partners, not always competitors. That means we have exceptional features we can offer the automotive designer:
            </p>
          </div>
          <div className='featuresexp'>
             <div className='featurename'>
                <svg width="29" height="27" viewBox="0 0 29 27" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M0.65625 26.3438C1.09375 26.7812 1.625 27 2.25 27H24.75C25.375 27 25.9062 26.7812 26.3438 26.3438C26.7812 25.9062 27 25.375 27 24.75V9.6375L24.75 11.8875V24.75H2.25V2.25H24.75V2.325V2.4L26.4 0.75C26.225 0.55 25.9938 0.375 25.7063 0.225C25.4188 0.0750001 25.1 0 24.75 0H2.25C1.625 0 1.09375 0.21875 0.65625 0.65625C0.21875 1.09375 0 1.625 0 2.25V24.75C0 25.375 0.21875 25.9062 0.65625 26.3438Z" fill="#B3B3B3"/>
                    <path d="M12.7875 20.7L4.4625 12.375L6.0375 10.8L12.7875 17.55L27.1125 3.225L28.6875 4.8L12.7875 20.7Z" fill="#C6A072"/>
                  </svg>
                  <p>
                     Unrivalled monitoring of new trends and innovations, so we can make adaptations and updates at any time
                  </p>
             </div>
             <div className='featurename'>
                <svg width="29" height="27" viewBox="0 0 29 27" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M0.65625 26.3438C1.09375 26.7812 1.625 27 2.25 27H24.75C25.375 27 25.9062 26.7812 26.3438 26.3438C26.7812 25.9062 27 25.375 27 24.75V9.6375L24.75 11.8875V24.75H2.25V2.25H24.75V2.325V2.4L26.4 0.75C26.225 0.55 25.9938 0.375 25.7063 0.225C25.4188 0.0750001 25.1 0 24.75 0H2.25C1.625 0 1.09375 0.21875 0.65625 0.65625C0.21875 1.09375 0 1.625 0 2.25V24.75C0 25.375 0.21875 25.9062 0.65625 26.3438Z" fill="#B3B3B3"/>
                    <path d="M12.7875 20.7L4.4625 12.375L6.0375 10.8L12.7875 17.55L27.1125 3.225L28.6875 4.8L12.7875 20.7Z" fill="#C6A072"/>
                  </svg>
                  <p>
                     Unlimited potential combinations of automotive styles, equipment, and mechanization systems me
                  </p>
             </div>
             <div className='featurename'>
                <svg width="29" height="27" viewBox="0 0 29 27" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M0.65625 26.3438C1.09375 26.7812 1.625 27 2.25 27H24.75C25.375 27 25.9062 26.7812 26.3438 26.3438C26.7812 25.9062 27 25.375 27 24.75V9.6375L24.75 11.8875V24.75H2.25V2.25H24.75V2.325V2.4L26.4 0.75C26.225 0.55 25.9938 0.375 25.7063 0.225C25.4188 0.0750001 25.1 0 24.75 0H2.25C1.625 0 1.09375 0.21875 0.65625 0.65625C0.21875 1.09375 0 1.625 0 2.25V24.75C0 25.375 0.21875 25.9062 0.65625 26.3438Z" fill="#B3B3B3"/>
                    <path d="M12.7875 20.7L4.4625 12.375L6.0375 10.8L12.7875 17.55L27.1125 3.225L28.6875 4.8L12.7875 20.7Z" fill="#C6A072"/>
                  </svg>
                  <p>
                     Enthusiastic encouragement and assistance for individual approaches,
                  </p>
             </div>
             <div className='featurename'>
                <svg width="29" height="27" viewBox="0 0 29 27" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M0.65625 26.3438C1.09375 26.7812 1.625 27 2.25 27H24.75C25.375 27 25.9062 26.7812 26.3438 26.3438C26.7812 25.9062 27 25.375 27 24.75V9.6375L24.75 11.8875V24.75H2.25V2.25H24.75V2.325V2.4L26.4 0.75C26.225 0.55 25.9938 0.375 25.7063 0.225C25.4188 0.0750001 25.1 0 24.75 0H2.25C1.625 0 1.09375 0.21875 0.65625 0.65625C0.21875 1.09375 0 1.625 0 2.25V24.75C0 25.375 0.21875 25.9062 0.65625 26.3438Z" fill="#B3B3B3"/>
                    <path d="M12.7875 20.7L4.4625 12.375L6.0375 10.8L12.7875 17.55L27.1125 3.225L28.6875 4.8L12.7875 20.7Z" fill="#C6A072"/>
                  </svg>
                  <p>
                    Vastly increased scope for creativity
                  </p>
             </div>
          </div>
        </div>
        {/* features end */}
        {/* customer choice start */}
        <div className='customerchoice'>
          <div className='customertextcont'>
            <div className='customerchoicetext'>
              <h6>Benefiting Customers</h6>
              <h2>Individualization of consumers choice</h2>
              <p>
                Collaborative manufacturing and design offer endless possibilities for the future. But the automotive industry is nothing without consumers. 
              </p>
              <p>
                We need to ensure that they will benefit from the EEVE Project revolution and that will see they are doing so. How can we achieve this? 
              </p>
              <p>
                Simple  complete individualization of choice
              </p>
            </div>
          </div>
          <div className='choices'>
            <div className='choice'>
              <h3>Accommodation of all color preferences</h3>
              <p>
                Henry Ford said any color was available as long as it was black;
                <br/> EEVE Projects approach is the polar opposite. Yes, we can give you framboise, tuberose, jacaranda or any shade you like.<br/> (And if we dont know what it is, we will find out.)
              </p>
            </div>
            <div className='choice'>
              <h3>Classic cars with contemporary technology</h3>
              <p>
              Potential to create classic cars with contemporary technology  so you can drive your 1950s dream machine with unprecedented levels of environmental quality and efficiency
              </p>
            </div>
            <div className='choice'>
              <h3>Any interiors and extras</h3>
              <p>
              Freedom to decide the type and combination of interiors and extras you want  from leather seats to walnut carbon or glass dashboards or high-tech dash cams
              </p>
            </div>
            <div className='choice'>
              <h3>Any type of propulsion</h3>
              <p>
              Freedom to decide the type of propulsion you want  gasoline, electric, hydrogen or alternative technologies
              </p>
            </div>
          </div>
          <div className='individual'>
            <h1>individualization</h1>
          </div>
        </div>
        {/* customer choice end */}
        {/* contact us start */}
        <div className='contactus'>
          <div className='contact'>
            <div className='frameimg'>
              <img src={frame} alt='frame' />
            </div>
            <div className='formcontact'>
                <h6>Contact us</h6>
                <h2>Didn't find answer to your question, contact us</h2>
                <form>
                  <div className='texrow'>
                    <div className='texinput'>
                      <input type='text' name='text'  placeholder='First Name' />
                    </div>
                    <div className='texinput'>
                      <input type='text' name='lname'  placeholder='Last Name' />
                    </div>
                  </div>
                  <div className='texrow'>
                    <div className='texinput'>
                      <input type='text' name='title'  placeholder='Title' />
                    </div>
                    <div className='texinput'>
                      <input type='email' name='email'  placeholder='Email' />
                    </div>
                  </div>
                  <div className='messinput'>
                    <textarea rows="8" placeholder='Your Request'>

                    </textarea>
                    </div>
                    <div className='discover' id='getintouch'>
                      <a href='#'>Get in Touch</a>
                      <svg width="34" height="16" viewBox="0 0 34 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M33.7071 8.70711C34.0976 8.31658 34.0976 7.68342 33.7071 7.29289L27.3431 0.928932C26.9526 0.538408 26.3195 0.538408 25.9289 0.928932C25.5384 1.31946 25.5384 1.95262 25.9289 2.34315L31.5858 8L25.9289 13.6569C25.5384 14.0474 25.5384 14.6805 25.9289 15.0711C26.3195 15.4616 26.9526 15.4616 27.3431 15.0711L33.7071 8.70711ZM0 9H33V7H0V9Z" fill="white"/>
                      </svg>
                  <div className='browncircle'>

                  </div>
                  </div>
                  
                </form>
            </div>
          </div>
        </div>
        <Footer />
    </div>
  )
}

export default Home