import React from 'react'
import { NavLink } from 'react-router-dom';
import './dashboard.css';
const Sidebar = () => {
  return (
    <div className='side_bar'>
        <ul>
         <li> <NavLink to="/dashboard/adminId" className='dashsidelink' activeClassName='active'> Dashboard </NavLink> </li>
         <li> <NavLink to="/pending-request/adminId" className='dashsidelink' activeClassName='active'> Pending Requests </NavLink> </li>
         <li> <NavLink to="/allusers/adminId" className='dashsidelink' activeClassName='active'>Total Users </NavLink> </li>
         <li> <NavLink to="/addusers/adminId" className='dashsidelink' activeClassName='active'> Add Users </NavLink> </li>
         <li> <NavLink to="/add-content/adminId" className='dashsidelink' activeClassName='active'> ADD Content </NavLink> </li>
            </ul>       
 
    </div>
  )
}

export default Sidebar