import React, { useEffect, useState } from 'react'
import { NavLink, useHistory, useParams } from 'react-router-dom'
import './header.css'
import logo from '../img/logo.png'
import profile from '../img/profile.png'
import Menu from './Menu'
import { Link } from 'react-router-dom'
import axios from 'axios'

const Header = () => {
  const params = useParams()
    const[userId, setUserId] = useState();

    const history = useHistory()
    const[isAdmin, setIsAdmin] = useState(false)
    let id = params.id
  
  const getUserProfile = async() => {
      await axios.get(`/userProfile`)
      .then((res) => {
          if(res.status===200){
            const data = res.data;
            console.log("data => ", data)
            setIsAdmin(data.isAdmin&&data.isAdmin)
          }
      }).catch((err) => {
          console.log(err)
          // history.push("/login")
      })
  }


    const setshowside=(e)=>{
        document.querySelector('.sidemnu').style.display='block';
       e.preventDefault()
    }
    const setsidemenu=(e)=>{
        document.querySelector('.sidemnu').style.display='none';
        e.preventDefault()
      }

      useEffect(() => {
        const userId = JSON.parse(localStorage.getItem("currUserId"))
        console.log(userId)
        setUserId(userId)
        getUserProfile()
      }, [])

      const logoutNow = () => {
        localStorage.clear()
      }
  return (
    <div>
       <div className='header'>
         <Link to='/' className='logo-menu'>
            <div className='logo'>
                <img src={logo} alt='eeve Project' />
                <h6>COLLABORATIVE AUTOMOBILE MANUFACTURING</h6>
            </div>
           </Link>
           <div className='headermenu'>
              <div className='headersearch'>
                  <form>
                      <input type='text' name='search' placeholder='Search' />
                  </form>
              </div>
              <div className='language'>
                  <form>
                      <select>
                          <option>EN</option>
                          <option>GER</option>
                      </select>
                  </form>
              </div>
              <div className='profile'>
                  <img src={profile} alt='profileicon' />
              </div>
              <div className='hammenu'>
              <svg width="26" height="18" viewBox="0 0 26 18" fill="none" xmlns="http://www.w3.org/2000/svg" onClick={setshowside}>
                    <rect y="0.5" width="26" height="2" fill="#BDBDBD"/>
                    <rect y="7.5" width="26" height="2" fill="#BDBDBD"/>
                    <rect y="14.5" width="26" height="2" fill="#BDBDBD"/>
                </svg>
                
              </div>
           </div>
        </div>
       
        <Menu />
        <div className='sidemnu'>
            <h6 onClick={setsidemenu}>&times;</h6>
          <ul>
          {isAdmin?
                <li>
                    <NavLink to={`/dashboard/${userId}`} className='sidelink' activeClassName='active'> Dashboard </NavLink>
                </li>:
                ''
            }
            <li>
                <NavLink to="/" className='sidelink' activeClassName='active'> Home </NavLink>
            </li>
            <li>
                <NavLink to={"/designgallery"} className='sidelink' activeClassName='active'> Design Gallery </NavLink>
            </li>
            <li>
                <NavLink to={"/partgallery"} className='sidelink' activeClassName='active'> Part Gallery  </NavLink>
            </li>
            <li>
                <NavLink to={"/cadgallery"} className='sidelink' activeClassName='active'> CAD Gallery </NavLink>
            </li>
            <li>
                <NavLink to={"/faq"} className='sidelink' activeClassName='active'> FAQ </NavLink>
            </li>
            <li>
                <NavLink to={`/setting/${userId}`} className='sidelink' activeClassName='active'> Setting </NavLink>
            </li>
          <li>
            <NavLink to={"/login"} className='navlink' onClick={(logoutNow)} activeClassName='active'> Logout </NavLink>
          </li>
          </ul>
          <ul id='mobileviewmn'>
          <li>
            <NavLink to="/profile" className='navlink' activeClassName='active'> Profile </NavLink>
          </li>
          <li>
            <NavLink to="/partupload" className='navlink' activeClassName='active'> Upload Center </NavLink>
           </li>
          <li>
            <NavLink to="/message" className='navlink' activeClassName='active'> Message </NavLink>
          </li>
          <li>
            <NavLink to="/forum" className='navlink' activeClassName='active'> Forum </NavLink>
          </li>
          <li>
            <NavLink to="/saved" className='navlink' activeClassName='active'> Saved </NavLink>
          </li>
          <li>
            <NavLink to="/setting" className='navlink' activeClassName='active'> Setting </NavLink>
          </li>
       </ul>
       </div>
    </div>
  )
}

export default Header