import React from 'react';
const AdminP = () => {
  return (
      <div className="admin" id='homeupload'>
          <h5>Choose manufacturer for whom you want to upload someting</h5>
          <div className='admininputs'>
                        <span className='input'>
                            <select>
                                <option>Chose the manufacturer</option>
                            </select>
                           
                            <button id='btn'>Enter</button>
                        </span>
                       
                    </div>
      </div>
  )
};

export default AdminP;
