import React, { Suspense }  from 'react';
import './App.css';
import Home from './components/home/Home';
import { BrowserRouter as Router,Switch,Route } from 'react-router-dom'
import PartG from './components/part-gallery/PartG';
import CadG from './components/cad-gallery/CadG';
import DesignUpload from './components/design-upload/Designupload';
import PartU from './components/part-upload/PartU';
import CadUploads from './components/cadupload/CadUploads';
// import Profile from './components/profile/Profile';
import Message from './components/Inbox/Message';
import Forum from './components/forum/forums/Forum';
import Saved from './components/saved/Saved';
import Settings from './components/setting/Settings';
import Faq from './components/faq/Faq';
import Login from './components/login/login';
import Register from './components/register/register';
import CompleteAccount from './components/complete_account/complete_account';
import SavedImg from './components/Savedimg/SavedImg';
import Adminupd from './components/adminup/Adminupd';
import PartA from './components/partadmin/PartA';
import Dashboard from "./components/dashboard/Dashboard"
import AddContent from "./components/dashboard/AddContent"
import DashHeader from "./components/dashboard/DashHeader"
import PendingRequest from "./components/dashboard/PendingRequest"
import Sidebar from "./components/dashboard/Sidebar"
import UploadForm from "./components/dashboard/UploadForm"
import UserRegister from "./components/dashboard/UserRegister"
import UserS from "./components/dashboard/UserS"
import { Provider } from 'react-redux';
import store from './redux/Store';
import { GrDashboard } from 'react-icons/gr';
import DesignerImages from './components/dashboard/DesignerImages';
import ManufacurerImages from './components/dashboard/ManufacurerImages';
import UploadFormmanu from './components/dashboard/UploadFormmanu';
import ConfirmEmail from './components/register/ConfirmEmail';
const Profile = React.lazy(() => import("./components/profile/Profile"))
const DesginG = React.lazy(() => import("./components/design_gallery/DesginG"))


function App() {
  return (
    <Suspense fallback={<div className='pre_loader'><h1>Loading...</h1></div>}>
      <Provider store={store}>
        <Router>
          <div>
            <Switch>
              <Route exact path='/' component={Home} />
              <Route exact path='/verify-email' component={ConfirmEmail} />
              <Route exact path='/designgallery' component={DesginG}  />
              <Route exact path='/partgallery' component={PartG} />
              <Route exact path='/cadgallery' component={CadG} />
              <Route exact path='/designupload/:id' component={DesignUpload} />
              <Route exact path='/partupload/:id' component={PartU}  />
              <Route exact path='/cadupload/:id' component={CadUploads}  />
              <Route exact path='/profile/:id' component={Profile}  />
              <Route exact path='/message/:id' component={Message}  />
              {/* <Route exact path='/forum' component={Forum}  /> */}
              <Route exact path='/forum/:id' component={Forum}  />
              <Route exact path='/saved/:id' component={Saved}  />
              <Route exact path='/setting/:id' component={Settings}  />
              <Route exact path='/faq/:id' component={Faq}  />
              <Route exact path='/login' component={Login}  />
              <Route exact path='/register' component={Register} />
              <Route exact path='/completeaccount/:id' component={CompleteAccount}  />
              <Route exact path='/savedimages/:id' component={SavedImg}  />
              <Route exact path='/adminupd' component={Adminupd}  />
              <Route exact path='/adminpartupd' component={PartA}  />
              <Route exact path='/dashboard/:id' component={Dashboard}  />
              <Route exact path='/add-content/:id' component={AddContent}  />
              <Route exact path='/dashboard-header/:id' component={DashHeader}  />
              <Route exact path='/pending-request/:id' component={PendingRequest}  />
              <Route exact path='/designer-images-approve/:id' component={DesignerImages}  />
              <Route exact path='/user-register/:id' component={UserRegister} />
              <Route exact path='/add-designer-images/:id' component={UploadForm} />
              <Route exact path='/add-manufacturer-images/:id' component={UploadFormmanu} />
              <Route exact path='/allusers/:id' component={UserS}  />
              <Route exact path='/manufacturer-images-approve/:id' component={ManufacurerImages}  />
            </Switch>
          </div>
        </Router>
        </Provider>
    </Suspense>
  );
}

export default App;
