import React, { useEffect, useState } from 'react'
import './saved.css';
import save1 from './save1.png'
import save2 from './save2.png'
import save3 from './save3.png'
import Header from '../header/Header';
import Footer from '../footer/Footer';
import { useHistory, useParams } from 'react-router-dom';
import { Spinner } from 'react-bootstrap';
import axios from 'axios';
const Saved = () => {
  const params = useParams()
  const history = useHistory()
  const[loader, setLoader] = useState(false)
  const[user, setUser] = useState()

  const getUserProfileInfo=async()=>{
    setLoader(true)
    await axios.get(`/userProfile`)
    .then((res) => {
        if(res.status===200){
            console.log(res.data)
            setUser(res.data)
        }
        setLoader(false)
    }).catch((err) => {
        setLoader(false)
        console.log(err)
        history.push("/login")
    })
}

useEffect(() => {
    getUserProfileInfo()
},[])

  return (
    <div>
      <Header/>
      <div className='containers'>
          <div className='topart'>
              <div className='salogans'>
                    <h6>My Solgan or discription</h6>
                    <h6>My Solgan or discription</h6>
              </div>
              <div className='prfile'>        
                      <img src={user&&user.file?`/images/${user.file}`:"/images/avt.jpg"} alt='saved pic 1' /> 
                      <h2>{user&&user.name} {user&&user.sur_name}</h2>         
              </div>
            </div> 
        <div className='headig'>
            <h1>All saved Images</h1>
        </div>
        <div className='prfiles'>
            {user?user.saved&&user.saved.map((imgs) => (
                <div className='save1'>
                    <img src={imgs.d_image} alt='saved pic 1' />
                </div>
            )):<div style={{
                    width: "80vw", 
                    height: "auto",
                    textAlign: "center"
                }}>
            <Spinner style={{}} animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
            </Spinner>
             </div>}
        </div>
      </div>
      <Footer/>
    </div>
  )
}

export default Saved