import axios from 'axios';
import React,{useEffect} from 'react'
import { useState } from 'react';
import { NavLink, useHistory, useParams } from "react-router-dom";
import './menu.css'

const Menu = () => {
  const history = useHistory()
  const[userId, setUserId] = useState()
  const[isAdmin, setIsAdmin] = useState(false)
const params = useParams()
let id = params.id

const pushTo = async() => {
    await axios.get(`/userProfile`)
    .then((res) => {
        if(res.status===200){
          const data = res.data;
          setIsAdmin(data.isAdmin&&data.isAdmin)
          if(data.func===""){
            history.push("/completeaccount/"+userId)
          }
          if(data.func === "Designer"){
            history.push("/designupload/"+userId)
          }
          if(data.func === "Manufacturer"){
            history.push("/partupload/"+userId)
          }
          if(data.func === "Technical draftsman/engineer"){
            history.push("/cadupload/"+userId)
          }
        }
    }).catch((err) => {
        console.log(err)
        history.push("/login")
    })
}

useEffect(() => {
  const userId = JSON.parse(localStorage.getItem("currUserId"))
  console.log(userId)
  setUserId(userId)
}, [])

  return (
    <div className='menubar'>
       <ul>
          <li>
            <NavLink to={"/profile/"+userId} className='navlink' activeClassName='active'> Profile </NavLink>
          </li>
          <li>
            <NavLink to="#" className='navlink' onClick={pushTo} activeClassName='active'> Upload Center </NavLink>
           </li>
          <li>
            <NavLink to={"/message/"+userId} className='navlink' activeClassName='active'> Message </NavLink>
          </li>
          <li>
            <NavLink to={"/forum/"+userId}className='navlink' activeClassName='active'> Forum </NavLink>
          </li>
          <li>
            <NavLink to={"/saved/"+userId} className='navlink' activeClassName='active'> Saved </NavLink>
          </li>
          <li>
            <NavLink to={"/setting/"+userId} className='navlink' activeClassName='active'> Setting </NavLink>
          </li>
       </ul>
    </div>
  )
}

export default Menu