import React from 'react';
import { useState } from 'react';
import { Spinner } from 'react-bootstrap';
import { useHistory, useParams } from 'react-router-dom';
import Footer from '../footer/Footer';
import LogoutHeader from '../header/LogoutHeader';
import firebase from "../../firebase"
import './cp.css'

function CompleteAccount() {
    const params = useParams()
    const history = useHistory()
    const[loader, setLoader] = useState(false)
    const[loaderS, setLoaderS] = useState(false)
    const[otps, setOtps] = useState()
    const[verify, setVerify] = useState();
    const[phone, setPhone] = useState()
    const[code, setCode] = useState()
    const[func, setFunc] = useState()
    const[company, setCompany] = useState()
    const[details, setDetails] = useState({
        country:"",
        state:"",
        city:"",
        zip:"",
        address:"",
    })

    const handleChange = (e) => {
        const name = e.target.name;
        const value = e.target.value;
        setDetails({
            ...details,
            [name]: value
        })
    }

    const setUpRecaptcha = () => {
        window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(
          "recaptcha-container",
          {
            size: "invisible",
            callback: function (response) {
              console.log("Captcha Resolved");
             // this.onSignInSubmit();
            },
            defaultCountry: "PAK",
          }
        );
      };

    const onSignInSubmit = async(e) => {   
        e.preventDefault();
        setUpRecaptcha();
        setLoaderS(true)
        let phoneNumber = '+92' + phone;

        let appVerifier = window.recaptchaVerifier;
            firebase
            .auth()
            .signInWithPhoneNumber(phoneNumber, appVerifier)
            .then(function (confirmationResult) {
                window.confirmationResult = confirmationResult;
                console.log("OTP is sent");
                setLoaderS(false)
            })
            .catch(function (error) {
                console.log(error);
                setLoaderS(false)
            });
    }

    const submitVerification = async(e) => {
        e.preventDefault()
        setLoader(true)
        const id = params.id

        window.confirmationResult
          .confirm(code)
          .then(async (result) => {
            // User signed in successfully.
            console.log("OTP verified")
            const res = await fetch("/verifySMS", {
                method: "POST",
                headers: {
                    "Content-type": "application/json",
                },
                body: JSON.stringify({
                    company: company,
                    func: func,
                    address: details.address,
                    country: details.country,
                    state: details.state,
                    zip: details.zip,
                    city: details.city,
                })
            })
            const data = await res.json()
            if(res.status === 200){
                setOtps(data)
            }
            history.push(`/profile/${id}`)
            setLoader(false)
          })
          .catch(function (error) {
            console.log(error);
            alert("Incorrect OTP");
            setLoader(false)
          });

        // try {
            
        // } catch (error) {
        //     console.log(error)
        //     setLoader(false)
        // }
    }

  return (
  <div>
      <LogoutHeader/>
        <div className='completeprof'>
        <div id="recaptcha-container"></div>
            <h1 className='login_heading'>COMPLETE YOUR ACCOUNT</h1>

            <div className='completeform'>
                <div className='formpati'>
                    <div className='formp1'>
                       <h6>In wich function do you join us?</h6>
                       <p>Do you join us in representation of a company?</p>
                       <div className='fcinput'>
                           <label htmlFor='company' id='company'>If yes, for which company:</label>
                           <br/>
                           <input type="text" name='company' onChange={(e)=>setCompany(e.target.value)} required placeholder='Company' />
                       </div>
                       <div className='fcinput'>
                           <label htmlFor='company'>If yes, for which function:</label>
                           <br/>
                           <select name='func' onChange={(e)=>setFunc(e.target.value)}>
                                <option value="Private">Private</option>
                                <option value="Designer">Designer</option>
                                <option value="Manufacturer">Manufacturer</option>
                                <option value="Technical draftsman/engineer">Technical draftsman/engineer</option>
                           </select>
                           {/* <input type="text" name='func' value={details.func} onchange={handleChange} required placeholder='Function' /> */}
                       </div>
                       <div className='verifyprofile'>
                           <p>Do you want to verify your account?</p>
                           <input type='radio' name='verify' onchange={(e)=>setVerify(e.target.value)} value="yes" checked/>
                           <label htmlFor='yes'>Yes</label>
                           <input type='radio' name='verify' onchange={(e)=>setVerify(e.target.value)} value="no" />
                           <label htmlFor='yes'>No</label>

                       </div>
                    </div>
                    <div className='formp2'>
                        <div className='inputs'>
                            <label htmlFor='country' id='country'>Country</label>
                            <br/>
                            <input type='text' name="country" onChange={handleChange} required placeholder='Country'/>
                        </div>
                        <div className='inputs'>
                            <label htmlFor='state'>State <span>(optional)</span> </label>
                            <br/>
                            <input type='text' name="state" onChange={handleChange} required placeholder='State'/>
                        </div>
                        <div className='addres'>
                            <div className='inputs'>
                                <label htmlFor='city'>City</label>
                                <br/>
                                <input type='text' name="city" onChange={handleChange} required placeholder='City'/>
                            </div>
                            <div className='inputs'>
                                <label htmlFor='zip'>Postal Code </label>
                                <br/>
                                <input type='text' name="zip" onChange={handleChange} required placeholder='Postal Code'/>
                            </div>
                        </div>
                        <div className='inputs'>
                            <label htmlFor='address'>Street, Building, Number / Apartment or office number</label>
                            <br/>
                            <input type='text' name="address" onChange={handleChange} required/>
                        </div>
                        <div className='smsphone'>
                            <div className='inputs'>
                                <label htmlFor='phone'>Write your cellphone number</label>
                                <br/>
                                <input type='text' name="phone" value={phone} onChange={(e)=>setPhone(e.target.value)} required/>
                            </div>
                            <div className='btns'>
                                <button onClick={onSignInSubmit}>
                                {loaderS?<>
                                    <Spinner animation="border" role="status">
                                        <span className="visually-hidden">Loading...</span>
                                    </Spinner>
                                </>: " Receive SMS "}
                                </button>
                            </div>
                        </div>
                        <div className='submitsms'>
                            <div className='inputs'>
                               
                                <input type='text' name="code" value={code} onChange={(e)=>setCode(e.target.value)} placeholder='Enter the SMS code'/>
                            </div>
                            <div className='btns'>
                                <button type='submit' onClick={submitVerification} id='comacount'>
                                    {loader?<>
                                        <Spinner animation="border" role="status">
                                            <span className="visually-hidden">Loading...</span>
                                        </Spinner>
                                    </>: "Submit"}
                                </button>
                                {/* <input type='submit' onClick={submitVerification} value={`Submit ${loader?'...': ''}`}  id='comacount'/> */}
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* <form className="register_form" id='mpt4'>
                <div className="reg_boxs">
                    <div className="input_box_profile">
                        
                    
                        <label htmlFor="email">In wich function do you join us?</label>
                        
            
                        <select className='' id="purpose">
                        <option>Designer</option>
                        <option>Manufacturer</option>
                        <option>Others</option>
                        </select>
                        
                        <label htmlFor="email" id='emaillabel'>Do you join us in representation of a Company?
                        <br />If yes, for which company?
                        </label>
                        
                        <input type="text" id='email' />
                        
                        <label htmlFor="email" className='sm-text'>If yes, in which function?</label>
                        
                        <input type="text" id='email' />
                        
                    
                        <p style={{"wordSpacing": "0.2em"}}>Do you want to verify your Profile?</p>
                        <div>
                            <input type="radio" id='group1'  name="monster" className='w-auto'/>
                            <label style={{"marginLeft":"10px"}} htmlFor="email" className='lineh'>Yes</label>
                            <input  type="radio" id='nope'  name="monster" className='w-auto' />
                            <label style={{"marginLeft":"10px"}} htmlFor="email" className='lineh'>No</label>
                        </div>
                        
                    </div>
                    
                    <div style={{marginTop: "-20px"}} className="input_box_profile">
                    
                        <label htmlFor="email" className='sm-text'>Country</label>
                    
                        <input type="text" id='email' />
                
                        <label htmlFor="email" className='sm-text'>State (optional)</label>
                    
                        <input type="text" id='email' />
                    
                        <label htmlFor="email" className='sm-text'>City</label>
                    
                        <input type="text" id='email' />
                    
                        <label>Street, Building, Number / Apartenmt or office number</label>
                        <input type="text" style={{"marginTop": "-100px"}} />
                    
                        <div className='extra_pro' id='extrat'>
                            <input  type="text" placeholder='write here your cellphone number' />
                            <button>recive an SMS</button>
                        </div>
                        <div style={{paddingTop:"50px"}} className='extra_pro'>
                            <input  type="text" placeholder='enter the SMS code' />
                            <button>Submit</button>
                        </div>
                    </div>
                </div>
            
            </form> */}
        </div>
        <Footer/>
  </div>
  );
}

export default CompleteAccount;
