import React, { useEffect, useState } from 'react'
import './forums.css'
import Header from '../../header/Header'
import Footer from '../../footer/Footer'
import Gview from './Gview'
import Mydisc from './Mydisc'
import SavedPosts from './SavedPosts'
import { Button, Form, Modal } from 'react-bootstrap'
import { useHistory, useParams } from 'react-router-dom'
import axios from 'axios'
const Forum = () => {
    const history = useHistory()
    const params = useParams()
       const [tabstoggle,setTabstoggle]=useState(1);
       const[questions, setQuestions] = useState()
       const[loader, setLoader] = useState(false)
       const[show, setShow] = useState(false)
       const[user, setUser] = useState()
       const[userId, setUserId] = useState()

       const[article, setArticle] = useState([])

       const handleClose = () => setShow(false);
       const handleShow = (e) => {
         setUserId(e.target.id)
         setShow(true)
     };

       const getUserProfileInfo=async()=>{
        setLoader(true)
        await axios.get(`/userProfile`)
        .then((res) => {
            if(res.status===200){
                console.log(res.data)
                setUser(res.data)
            }
            setLoader(false)
        }).catch((err) => {
            setLoader(false)
            console.log(err)
            history.push("/login")
        })
    }

    
    const getAllArticleQuestions = async(e) => {
        try {
            const result = await fetch("/getAllArticleQuestions", {
                method: "GET",
                headers: {
                    "Content-type": "application/json"
                }
            })
            const data = await result.json()
            if(data.status===200){
                setShow(false)
                setArticle(data?data:[])
            }
            console.log("data",data)
        } catch (error) {
            console.error(error)
        }
    } 
    
    useEffect(() => {
        getUserProfileInfo()
        getAllArticleQuestions()
    },[])

       const toggletabs=(index)=>{
           setTabstoggle(index);
       }
    const showpopup=(e)=>{
        const popdiv=document.querySelector('.popsmain');
        popdiv.style.display='flex';
     e.preventDefault();
    }
    const hidepopup=(e)=>{
        const popdiv=document.querySelector('.popsmain');
        popdiv.style.display='none';
     e.preventDefault();
    }

    const addQuestions = async(e) => {
        const user_id = params.id
        try {
            const result = await fetch("/addArticleOrQuestion", {
                method: "POST",
                headers: {
                    "Content-type": "application/json"
                },
                body: JSON.stringify({question:questions})
            })
            const data = await result.json()
            if(data.status===200){
                setShow(false)
                getAllArticleQuestions()
            }
            console.log(data)
        } catch (error) {
            console.error(error)
        }
    }


  return (
    <div>
    <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                    <Form.Label>Add Question</Form.Label>
                    <Form.Control type="email" onChange = {(e)=>setQuestions(e.target.value)} />
                </Form.Group>
            </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={addQuestions}>
            Add Question
          </Button>
        </Modal.Footer>
      </Modal>
        <Header />
        <div className='mobiletabs'>
              <div className='forumtabs'>
                   <div className={tabstoggle === 1 ? "tab active-ftabs" : "tab"}>
                       <button onClick={()=>toggletabs(1)}>General view</button> 
                   </div>
                   <div className={tabstoggle === 2 ? "tab active-ftabs" : "tab"}>
                       <button onClick={()=>toggletabs(2)}>My discussions</button> 
                   </div>
                   <div className={tabstoggle === 3 ? "tab active-ftabs" : "tab"}>
                       <button onClick={()=>toggletabs(3)}>Saved Posts</button> 
                   </div>
                   <div className={tabstoggle === 4 ? "tab active-ftabs" : "tab"}>
                       <button onClick={()=>toggletabs(4)}>Followed Persons</button> 
                   </div>
               </div>
        </div>
        <div className='forumsect'>
           <div className='forumlsec'>
               <h6>Themes:</h6>
               <button id='chtheme' onClick={showpopup}>Choose themes</button>
               <div className='popsmain'>
                    <div className='themes-popup'>
                        <h5 onClick={hidepopup}>&times;</h5>
                        <h6>Themes:</h6>
                        <div className='forumfilters'>
                        <div className='filters'>
                                <select>
                                    <option>Main category</option>
                                </select>
                        </div>
                        <div className='filters'>
                                <select>
                                    <option>Second category</option>
                                </select>
                        </div>
                        <div className='filters'>
                                <select>
                                    <option>Third category</option>
                                </select>
                        </div>
                        <div className='filters'>
                                <select>
                                    <option>Sub category</option>
                                </select>
                        </div>
                        <div className='filters'>
                                <button>Filter</button>
                        </div>
                        
                    </div>
                    </div>
               </div>
             
               <div className='forumfilters'>
                   <div className='filters'>
                        <select>
                            <option>Main category</option>
                        </select>
                   </div>
                   <div className='filters'>
                        <select>
                            <option>Second category</option>
                        </select>
                   </div>
                   <div className='filters'>
                        <select>
                            <option>Third category</option>
                        </select>
                   </div>
                   <div className='filters'>
                        <select>
                            <option>Sub category</option>
                        </select>
                   </div>
                  
               </div>
               <div className='forumbutns'>
                   <div className='fbtns'>
                       <button>Solution Proposals</button>
                   </div>
                   <div className='fbtns'>
                       <button>News in design</button>
                   </div>
                   <div className='fbtns'>
                       <button>News in technology</button>
                   </div>
                   <div className='fbtns'>
                       <button onClick={handleShow}>Add an artikel or question</button>
                   </div>
               </div>
           </div>
           <div className='forumrsec'>
               <div className='forumtabs'>
                   <div className={tabstoggle === 1 ? "tab active-ftabs" : "tab"}>
                       <button onClick={()=>toggletabs(1)}>General view</button> 
                   </div>
                   <div className={tabstoggle === 2 ? "tab active-ftabs" : "tab"}>
                       <button onClick={()=>toggletabs(2)}>My discussions</button> 
                   </div>
                   <div className={tabstoggle === 3 ? "tab active-ftabs" : "tab"}>
                       <button onClick={()=>toggletabs(3)}>Saved Posts</button> 
                   </div>
                   <div className={tabstoggle === 4 ? "tab active-ftabs" : "tab"}>
                       <button onClick={()=>toggletabs(4)}>Followed Persons</button> 
                   </div>
               </div>
               <div className={tabstoggle === 1 ? "active-ftabscontent" : "ftabs"}>
                  <Gview article={article} />
               </div>
               <div className={tabstoggle === 2 ? "active-ftabscontent" : "ftabs"}>
                   <Mydisc/>
               </div>
               <div className={tabstoggle === 3 ? "active-ftabscontent" : "ftabs"}>
                    <SavedPosts/>
               </div>
               <div className={tabstoggle === 4 ? "active-ftabscontent" : "ftabs"}>
                    <Gview/>
               </div>
              
           </div>
        </div>
        <Footer/>
    </div>
  )
}

export default Forum