import React, { useEffect, useState } from 'react'
import FileBase64 from 'react-file-base64';
import eag from '../img/emptyimg.png'
import eagle from '../img/Bugatti 1.jpg';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import { Spinner } from 'react-bootstrap';
const DesignUp = ({getFileData}) => {
  const[loader, setLoader] = useState(false)
  const[files, setFiles] = useState()
  const params = useParams()
  const[user, setUser] = useState()
  const[imgs, setImgs] = useState()
  const[images, setImages] = useState([])
  const[imageBack, setImageBack] = useState([])
  let fileArr = [];
  let fileObj = []

  const handleDone = async({base64}) => {
    setLoader(true)
    fileObj.push(base64)
    // if(designer_imgs.length > 12){
    //     alert("Images upload limit exceeded.")
    //     setLoader(false)
    //     return
    // }

    // for(var i=0; i<fileObj[0].length; i++){
        // const name = designer_imgs[i]
        // fileArr.push(URL.createObjectURL(fileObj[0][i]))
        setImages([...images, base64])
    // }
    setLoader(false)
  }
  getFileData(images)

  const removeImage = (e)=> {
    const imge = e.target.id;
    const arr = images.filter(item => item!==imge)
    console.log("filtered",arr)
    console.log(images)
    setImages(arr)
  }

  const getDesignerImages = async(e) => {
    try {
        const res = await fetch("/getDesignerImages", {
            method: "GET",
            headers: {
                "content-type": "application/json"
            }
        })
        const data = await res.json();
        if(res.status===200){
            setImgs(data)
            data&&data.map(d => {
                d.designer_imgs.forEach(im => {
                    console.log(im)
                })
            })
            console.log(data)
        }
    } catch (error) {
        console.error(error)
    }
  }

  useEffect(() => {
    getDesignerImages()
  }, [])

  return (
    <div>
        <div className='uplcenter'>
            <div className='imageupload' id='designupimg'>
                <div className='imgup'>
                    <img src={images[0]?images[0]:eagle} alt='image' />
                    <h2>Cover image</h2>
                </div>
                <div className='btnup'>
                    <form action="/uploadDesignerImages" method='POST' encType='multipart/form-data'>
                    <label htmlFor="inputTag">
                        {/* {loader?<>
                            <Spinner animation="border" role="status">
                                <span className="visually-hidden">Loading...</span>
                            </Spinner>
                        </>: "Change Image"} */}
                        <FileBase64
                            multiple={ false }
                            onDone={handleDone} />

                        {/* <input multiple onChange={handleDone} id='inputTag' type="file" hidden /> */}
                    </label>
                    </form>
                </div>
            </div>
            <div className='uploadedimg'>
                <div className='uplsecimg'>
                    <img src={images[0]?images[0]:eagle} alt='img'/>
                    <h6 className='partcoverimg'>Cover image</h6>
                    <span className='partpicdelete'><i className='fas fa-trash-alt'></i></span>
                </div>
                {images&&images.map((imgs, index) => (
                    <div className='uplsecimg' key={index} >
                        <img src={imgs} alt='img'/>
                        <span className='partpicdelete'><i id={imgs} onClick={removeImage} className='fas fa-trash-alt'></i></span>
                    </div>
                ))}
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                    
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                 
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>

                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                 
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
                <div className='uplsecimg'>
                    <img src={eag} alt='img'/>
                   
                </div>
            </div>
        </div>
    </div>
  )
}

export default DesignUp