import React from 'react'
import { NavLink } from 'react-router-dom'
import './footer.css'
const Footer = () => {
  return (
    <div>
        <div className='footer'>
            <div className='footerlist'>
              <NavLink to="uploadcenter" className='footlink' activeClassName='active'> Faq </NavLink>
              <NavLink to="uploadcenter" className='footlink' activeClassName='active'> Support </NavLink>
            </div>
            <div className='footerlist'>
              <NavLink to="uploadcenter" className='footlink' activeClassName='active'> Contact US </NavLink>
              <NavLink to="uploadcenter" className='footlink' activeClassName='active'> Press </NavLink>
            </div>
            <div className='footerlist'>
              <NavLink to="uploadcenter" className='footlink' activeClassName='active'> Invester relation </NavLink>
              <NavLink to="uploadcenter" className='footlink' activeClassName='active'> Legal Notice </NavLink>
            </div>
            <div className='footerlist'>
              <NavLink to="uploadcenter" className='footlink' activeClassName='active'> Security </NavLink>
              <NavLink to="uploadcenter" className='footlink' activeClassName='active'> Term of use </NavLink>
            </div>
        </div>
    </div>
  )
}

export default Footer