import React from 'react'
import Footer from '../footer/Footer';
import Header from '../header/Header';
import './faq.css'
const Faq = () => {
    const showanswer=(e)=>{
        let  showdiv=e.target.parentNode;
        let    iconchange=e.target.childNodes[1];
        if(showdiv.className== 'qhead'){
            showdiv=showdiv.lastChild;
          
            console.log(iconchange)
        }
        else{
            showdiv = showdiv.parentNode.lastChild;
            iconchange=e.target.parentNode.childNodes[1];
        }
        if(showdiv.style.display == 'block'){
            showdiv.style.display='none';
            iconchange.classList.remove('fa-angle-down')
            iconchange.classList.add('fa-angle-right')
        }
        else{
            showdiv.style.display='block';
            iconchange.classList.remove('fa-angle-right');
            iconchange.classList.add('fa-angle-down')

        }
        console.log(showdiv.className);
        console.log(iconchange)
        e.preventDefault();
    }
  return (
      <div>
          <Header/>
        <div className='mfaq'>
            <h1>Frequently asked questions</h1>
        <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>Where does the EEVE PROJECT name come from?</h2>
                <div className='manswer'>
                    <p>The name EEVE is a modified form of the name EVE (Eva, Ewa, and Hawa) and symbolizes the origin of life. We didnt want to try and create an automotive company version 2.0, so we decided to reinvent the automotive industry and start from zero.</p>
                </div>
            </div>
        </div>
        <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>What can EEVE PROJECT do for you?</h2>
                <div className='manswer'>
                    <p>EEVE PROJECT can make your designs popular. You will remain in possession of them for as long as you want and later, you can choose whether to settle for a fee based on the number of cars produced or you can sell your design. You determine the price, giving you total control over your earnings.<br />
                    We are looking to set up five design and development studios in the following locations: North America, South America, Europe, the Middle East, and Asia.<br />
                    As soon as it is clear which design we are going to support, we will invite the stakeholders to work together on the project.<br />
                    As an engineer or technical draftsman, you will have a much easier time turning your innovative ideas into reality at EEVE PROJECT. We develop these ideas in the present but visualize them in the future, and we are open to ideas that may be considered science fiction today but state of the art in 20 years. Additionally, you can draw parts and charge a fee. You can later sell them or offer single designs for free download to help increase your reputation.<br />Suppliers are given more say, fairer pricing, and the choice of project they want to participate in.
    </p>
                </div>
            </div>
        </div>
        {/* <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>The Services are free !</h2>
                <div className='manswer'>
                    <p>The services are free. We are trying to keep the system free for as long as possible. We may finance ourselves by fees or advertising, and there may be a membership fee/we may act as a manufacturing company in the future. In any case, nothing happens without your consent.</p>
                </div>
            </div>
        </div> */}
        <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>Will I always have ownership of my design?</h2>
                <div className='manswer'>
                    <p>Yes, unless you sell it or assign your rights in an isolated contract.</p>
                </div>
            </div>
        </div>
        <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>What do you mean by AFV?</h2>
                <div className='manswer'>
                    <p>AFV stands for "Advance Flight Vehicle." The terms "air cab" or "advanced flight mobility" are also used. These are modern flying objects with electric or combustion engines that are used to transport people in the air.</p>
                </div>
            </div>
        </div>
        <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>How and where are the cars manufactured?</h2>
                <div className='manswer'>
                    <p>Once the community, automotive suppliers, potential buyers, and investors have decided to make a vehicle or AFV a reality, the designers, prototype engineers, model builders, and leading suppliers get together and start work on the development. A clay model is then created, and at the same time, the potential drive systems are explored. The next step of the process depends on the expected number of units. Very small productions can be handmade in carbon fiber and/or with a lattice tube frame. For higher quantities, contract manufacturers can take over the order, but it should be noted that we are a growing innovative community that does not have to commit to a particular process.</p>
                </div>
            </div>
        </div>   <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>When will the first cars be produced?</h2>
                <div className='manswer'>
                    <p>Depending on the development, we see the possibility of starting production two years after the official launch of the EEVE PROJECT.</p>
                </div>
            </div>
        </div>
        <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>Can I withdraw my design?</h2>
                <div className='manswer'>
                    <p>As long as no separate agreement is signed, you can withdraw your design at any time.</p>
                </div>
            </div>
        </div>
        <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>Are you open to investment?</h2>
                <div className='manswer'>
                    <p>Yes, we are open to investment; we want to revolutionize the automotive industry to benefit humanity. The design and construction of new vehicles will cost us only a fraction of what existing car manufacturers have to spend because of the decentralized system. However, we are thinking ahead, such as the bioreaction engine proposed by Jason Es-Salim, in which a biological reaction generates a rapid, forceful motion that feeds a flywheel to provide the necessary propulsion needed to move a vehicle. <br />There are many more innovative ideas from outstanding designers, engineers, and researchers waiting to be put into action, and we invite investors to be part of this development.</p>
                </div>
            </div>
        </div>
        <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>Who will take care of the quality assurance of the parts?</h2>
                <div className='manswer'>
                    <p>It is expected that any supplier company can offer parts. The EEVE PROJECT will ensure the quality assurance of certain parts in cooperation with material testing institutes, technical inspection associations, or authorities. Of course, established suppliers can also provide technical test certificates themselves.</p>
                </div>
            </div>
        </div>
        <div className='mqa'>
            <div className='qhead'>     
                <h2 onClick={showanswer}>  <i className='fas fa-angle-right'></i>What will the cars be called?</h2>
                <div className='manswer'>
                    <p>EEVE PROJECT allows many different projects to be realized by different people and so the name of the car will be decided on by the designers or those who finance the project, and existing automotive companies are also free to use this platform.</p>
                </div>
            </div>
        </div>
        </div>
    <Footer/>
    </div>
  )
}

export default Faq