import React ,{useRef, useState}from 'react'
import FileBase64 from 'react-file-base64';
import Footer from '../footer/Footer';
import Header from '../header/Header';
import './settings.css'
import axios from "axios"
import { useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Spinner } from 'react-bootstrap';

const Settings = () => {
    const history = useHistory()
    const params = useParams()
    const imgRef = useRef()
    const[imgLoader, setImgLoader] = useState(false)
    const[user, setUser] = useState()
    const[data, setData] = useState()
    const[ingenieur, setIngenieur] = useState("mainFolder")
    const[manufacturer, setManufaturer] = useState("mainFolder")
    const[designer, setDesigner] = useState("mainFolder")
    const[projectTwo, setProjectTwo] = useState("mainFolder")
    const[file, setFile] = useState('');
    const[loader, setLoader] = useState(false)
    const[profileDetails, setProfileDetails] = useState({
      name: "",
      sur_name: "",
      company: "",
      country: "",
      state: "",
      zip: "",
      address: "",
      phone: "",
      email: "",
      verify_contact: "",
      func: "",
      f1: "",
      f2: "",
      f3: "",
      f4: "",
      publicEmail: "",
      company_description: "",
    })

    const getUserProfileInfo=async()=>{
      await axios.get(`/userProfile`)
      .then((res) => {
          if(res.status===200){
              setUser(res.data)
              const data = res.data
              setProfileDetails({
                name: data&&data.name,
                sur_name: data&&data.sur_name,
                company: data&&data.company,
                country: data&&data.country,
                state: data&&data.state,
                zip: data&&data.zip,
                address: data&&data.address,
                phone: data&&data.phone,
                email: data&&data.email,
                verify_contact: data&&data.verify_contact,
                func: data&&data.func,
                f1: data&&data.f1,
                f2: data&&data.f2,
                f3: data&&data.f3,
                f4: data&&data.f4,
                publicEmail: data&&data.publicEmail,
                company_description: data&&data.company_description
              })
          }
      }).catch((err) => {
          console.log(err)
          history.push("/login")
      })
  }

  useEffect(() => {
      getUserProfileInfo()
  },[])

  const uploadPorfile = async(e) => {
    setImgLoader(true)
    const file = e.target.files[0]
        const formData = new FormData()
        formData.append("file", file)
        try {
            const data = await axios.post("/updateFile", formData,{ 
                headers: {
                    "Content-Type": "multipart/form-data"
                }
            }).then((res) => {
                getUserProfileInfo()
                setImgLoader(false)
            }).catch((error) => {
                alert("Error occured please try again")
                setImgLoader(false)
            })
            
        } catch (error) {
            console.log(error)
            setImgLoader(false)
        }
  }

  const handleChange = (e) => {
    const name = e.target.name;
    const val = e.target.value

    setProfileDetails({
      ...profileDetails,
      [name]:val
    })
  }


  const submitData= async (e)=>{
    e.preventDefault();
    setLoader(true)
    const id = params.id
        try {
            const res = await fetch("/updateUserProfile", {
                method: "POST",
                headers: {
                    "content-type": "application/json",
                },
                body: JSON.stringify({
                    name: profileDetails.name,
                    sur_name: profileDetails.sur_name,
                    company: profileDetails.company,
                    country: profileDetails.country,
                    state: profileDetails.state,
                    zip: profileDetails.zip,
                    address: profileDetails.address,
                    phone: profileDetails.phone,
                    email: profileDetails.email,
                    verify_contact: profileDetails.verify_contact,
                    func: profileDetails.func,
                    f1: profileDetails.f1,
                    f2: profileDetails.f2,
                    f3: profileDetails.f3,
                    f4: profileDetails.f4,
                    publicEmail: profileDetails.publicEmail,
                    company_description: profileDetails.company_description,
                    ingenieur, manufacturer, designer, projectTwo
                })
            })
            const data = await res.json()
            console.log(data)
            if(res.status === 200){
                setLoader(false)
                alert("Profile updated successfull...")
                history.push("/profile/"+id)
            }else{
               setLoader(false)
                alert("Error occured, Please try again")
                window.location.reload()
            }
        } catch (error) {
            console.error(error)
            setLoader(false)
            alert(error.message)
        }
}

  return (
    <div>
      <Header/>
      <div className='setting_main'>
        <div className='setting-left'>
          <div className='img-div'>
            <div className='imgset'>
                <div className='imag'>
                    <img src={user&&user.file?`/images/${user.file}`:"/images/avt.jpg"} />
                </div>
                <label>{imgLoader?<>
                  <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                  </Spinner>
                </>:"Change Image"}
                  <input type="file" ref={imgRef} onChange={uploadPorfile} className='file' name='file' />
                </label>
            </div>
            <div className='name'>
                <h2>{user&&user.name}</h2>
            </div>
          </div>
          <div className='info-form'>
              <form>
                <textarea name='company_description' value={profileDetails.company_description} onChange={handleChange} />
                <p>What do you want to tell the World?</p>
                <input type="text" name='publicEmail' onChange={handleChange} placeholder='' />
                <p>Your public email   (optional)</p>

              </form>
          </div>
          
        </div>
        <div className='setting-right'>
          <form>
              <div className='inputs'>
              <div className='label'>  
                      <label>Name</label>
                      </div>
                              <input type="text" value={profileDetails.name} name='name' onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
                  <div className='label'>   
                        <label>Last name</label>
                  </div>
                              <input type="text" value={profileDetails.sur_name} name='sur_name' onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
              <div className='label'>  
                      <label>Company Name</label>
                      </div>
                              <input type="text" value={profileDetails.company} name='company' onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
              <div className='label'>  
                      <label>COUNTRY</label>
                      </div>
                              <input type="text" value={profileDetails.country} name='country' onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
              <div className='label'>  
                      <label>STATE</label>
                      </div>
                              <input type="text" name='state' value={profileDetails.state} onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
              <div className='label'>  
                      <label>ZIPCODE, CITY, District </label>
                      </div>
                              <input type="text" name='zip' value={profileDetails.zip} onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
              <div className='label'  id='address'>  
                      <label>Street, Building, no, Apartment </label>
                      </div>
                              <input type="text" value={profileDetails.address} name='address' onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
              <div className='label'>  
                      <label>CELL PHONE NUMBER</label>
                      </div>
                              <input type="text" value={profileDetails.phone} name='phone' onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
              <div className='label'>  
                      <label>Email</label>
                      </div>
                              <input type="text" value={profileDetails.email} name='email' onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
              <div className='label' id='pflabel'>  
                      <label>Profile Status</label>
                      </div>
                      <div class="switch-button">
                        <input className="switch-button-checkbox" type="checkbox"></input>
                        <label className="switch-button-label" for="">
                          <span className="switch-button-label-span">Verified</span>
                        </label>
                      </div>      
              </div>
              <div className='inputs'>
              <div className='label'>  
                      <label>Do you want to verify your acount?How would you like contacted by us?</label>
                      </div>
                              <input type="text" name='verify_contact' value={profileDetails.verify_contact} onChange={handleChange} placeholder='' />      
              </div>
              <div className='inputs'>
              <div className='label'>  
                      <label>Type of your Account</label>
                      </div>
                      <span className='select'>
                           <select name='func' value={profileDetails.func} sele onChange={handleChange}>
                                <option value="">Function</option>
                                <option value="Private">Private</option>
                                <option value="Designer">Designer</option>
                                <option value="Manufacturer">Manufacturer</option>
                                <option value="Technical draftsman/engineer">Technical draftsman/engineer</option>
                           </select>
                              {/* <input type="text" value={profileDetails.func} name='func' onChange={handleChange} placeholder='' />
                              <i className="fa fa-caret-down" aria-hidden="true"></i>  */}
                          </span>
              </div>
              <div className='note'>
                <p>Please Note:</p>
                <p>It is intended that users can only perform one role.</p>
                <p>For this reason, if you change the roles, the image you uploaded in the previos function will no longer visible.</p>
                <p id='contact'>If your case is particular, dont hesitate to contact us. <a href='#'><i className="fa fa-envelope"></i></a></p>

              </div>
              <div className='inputs' id='fa'>
              <div className='label'>  
                      <label>2 FA</label>
                      </div>
                    <div className='fainputs'> 
                      <input type="text" name='f1' value={profileDetails.f1} onChange={handleChange} placeholder='' /> 
                      <input type="text" name='f2' value={profileDetails.f2} onChange={handleChange} placeholder='' />
                      <input type="text" name='f3' value={profileDetails.f3} onChange={handleChange} placeholder='' /> 
                      <input type="text" name='f4' value={profileDetails.f4} onChange={handleChange} placeholder='' />    
                    </div>  
              </div>
          </form>
          <div className='vip'>
            <p>Select which messages will be placed in the VIP folder:</p>
            
              <div className='vipdoolders'>
                    <h6>Project two:</h6>
                    <form>
                        <input type="radio" id="yes" name="projectTwo" onChange={(e)=>setProjectTwo(e.target.value)} value="mainFolder" checked />
                          <label for="html" id='mainfolder'>Main folder</label>
                        <input type="radio" id="no" name="projectTwo" onChange={(e)=>setProjectTwo(e.target.value)} value="vipFolder" />
                          <label for="css">VIP folder</label>
                    </form>

              </div>
              <div className='vipdoolders'>
                    <h6>Manufacturer:</h6>
                    <form>
                        <input type="radio" id="yes" name="manufacturer" onChange={(e)=>setManufaturer(e.target.value)} value="mainFolder" checked />
                          <label for="html" id='mainfolder' className='mainf'>Main folder</label>
                          <input type="radio" id="no" name="manufacturer" onChange={(e)=>setManufaturer(e.target.value)} value="vipFolder" />
                          <label for="css">VIP folder</label>
                    </form>

              </div>
              <div className='vipdoolders'>
                    <h6>Designer:</h6>
                    <form>
                        <input type="radio" id="yes" name="designer" onChange={(e)=>setDesigner(e.target.value)} value="mainFolder" checked />
                          <label for="html" id='mainfolder'>Main folder</label>
                          <input type="radio" id="no" name="designer" onChange={(e)=>setDesigner(e.target.value)} value="vipFolder" />
                          <label for="css">VIP folder</label>
                    </form>

              </div>
              <div className='vipdoolders'>
                    <h6>Ingenieur:</h6>
                    <form>
                        <input type="radio" id="yes" name="ingenieur" onChange={(e)=>setIngenieur(e.target.value)} value="mainFolder" checked />
                          <label for="html" id='mainfolder'>Main folder</label>
                          <input type="radio" id="no" name="ingenieur" onChange={(e)=>setDesigner(e.target.value)} value="vipFolder" />
                          <label for="css">VIP folder</label>
                    </form>
              </div>
          </div>
          <button onClick={submitData}>
            Update Profile <span>{loader?
            <>
              <Spinner animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
              </Spinner>
            </>:
            ""
            }</span>
          </button>
        </div>
      </div>
      <Footer/>
    </div>
  )
}

export default Settings