import React,{useState} from 'react'
import down from './downn.png'
import eag from '../img/emptyimg.png'
import FileBase64 from 'react-file-base64';

const CadUpart = ({getFileData}) => {
    const[loader, setLoader] = useState(false)
    const[files, setFiles] = useState()
    const[user, setUser] = useState()
    const[imgs, setImgs] = useState()
    const[images, setImages] = useState([])
    const[imageBack, setImageBack] = useState([])
    let fileArr = [];
    let fileObj = []

    const handleDone = async({base64}) => {
        setLoader(true)
        fileObj.push(base64)
        setImages([...images, base64])
        setLoader(false)
      }
      getFileData(images)

      const removeImage = (e)=> {
        const imge = e.target.id;
        const arr = images.filter(item => item!==imge)
        console.log("filtered",arr)
        console.log(images)
        setImages(arr)
      }

  return (
    <div>
    <div className='uplcenter'>
        <div className='imageupload' id='designupimg'>
            <div className='imgup'>
                <img src={images&&images[0]?images[0]:down} alt='image' />
                <h2>Cover image</h2>
            </div>
            <div className='btnup'>
                <FileBase64
                    multiple={ false }
                    onDone={handleDone} />
            </div>
        </div>
        <div className='uploadedimg'>
            <div className='uplsecimg'>
                <img src={images&&images[0]?images[0]:down} alt='img'/>
                <h6 className='partcoverimg'>Cover image</h6>
                <span className='partpicdelete'><i className='fas fa-trash-alt'></i></span>
            </div>
            {images&&images.map((imgs, index) => (
                <div className='uplsecimg' key={index} >
                    <img src={imgs} alt='img'/>
                    <span className='partpicdelete'><i id={imgs} onClick={removeImage} className='fas fa-trash-alt'></i></span>
                </div>
            ))}
        </div>
    </div>
</div>
  )
}

export default CadUpart