import React ,{useEffect, useState}from 'react';
import { Link, useHistory } from 'react-router-dom';
import './login.style.css';
import LogoutHeader from '../header/LogoutHeader'
import Footer from '../footer/Footer'
import { Spinner } from 'react-bootstrap';
function Login() {
    const history = useHistory()
    const[loading, setLoading] = useState(false)
    const [user,setUser]=useState({
        email:'',
        password:''
    });

    const onchange=e=> {
        setUser({...user,[e.target.name]:e.target.value})
    }

    const onSubmit=async(e)=>{
        e.preventDefault();
        setLoading(true)
        const {email,password}=user;
        try {
            const res = await fetch("/signin", {
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify({email, password})
            })
            const data = await res.json()
            console.log(data)
            if(res.status === 200){
                localStorage.setItem("func", JSON.stringify("no"))
                setLoading(false)
                console.log("data", data)
                if(data.user._id){
                    localStorage.setItem("currUserId", JSON.stringify(data.user._id))
                    history.push(`/profile/${data.user._id}`)
                }else{
                    alert("Error occured while loggin in, Please try again")
                }
            }else{
                alert("Invalid email or password")
                setLoading(false)
                window.location.reload()
            }
        } catch (error) {
            setLoading(false)
            console.error(error)
        }
    }

    useEffect(async() => {
    }, [])

  return (
  <div>
    <div className='Login'>
        <h1 className='login_heading'>Login to your account</h1>
        <div className='loginform'>
            <form>
                <div className='inputs'>
                    <label htmlFor='email'>Email</label>
                    <br/>
                    <input type='email' name="email" onChange={onchange} required placeholder='Email'/>
                </div>
                <div className='inputs'>
                    <label htmlFor=''>Password</label>
                    <br/>
                    <input type='password' name="password" onChange={onchange} required  placeholder='Password'/>
                </div>
                <div className='loginaction'>
                    <div className='btns'>
                        {/* <input type='submit' value="Login" onClick={onSubmit} className='submit'/> */}
                        <button type='submit' className='submit' onClick={onSubmit}>
                        {loading?
                            <Spinner animation="border" role="status">
                                <span className="visually-hidden">Loading...</span>
                            </Spinner>
                        :"Login"}
                        </button>
                    </div>
                    <div className='fpassword'>
                        <p>Forgot your password?</p>
                    </div>
                </div>
            </form>
            <div className='regnow'>
              <h3>Don`t have an account?  <Link to='/register' className='reglogin'>Register</Link> </h3>
            </div>
        </div>





        {/* <div className='login_form'>
        <form onSubmit={onSubmit}>
        <div className="login_ins_c">
                <div className="login_ins">
                    <label htmlFor="email">Email</label>
                    <br />
                    <input type="text" id='email' name='email' onChange={onchange} required />
                    <br />
                </div>
                <div className="login_ins">
                    <label htmlFor="pass">Password</label>
                    <br />
                    <input type="password" id='pass' name='password' onChange={onchange} required />
                </div>
        </div>
            <div className='login_others'>
                <span><Link to='#'  id='forgots'>Forget Your Password?</Link></span>
                <input type='submit' value='submit' className='submit' />
                <br />
                <p>Don't Have Account? <span className='sml_rg_btn'><Link to='/register' className='links'>Register</Link></span></p>
                
            </div>
            </form>
        </div> */}
    </div>
    <Footer/>
  </div>);
}

export default Login;
