import React ,{useEffect, useState}from 'react'
import Header from '../header/Header'
import car from '../img/cad.png'
import Popup from 'reactjs-popup';
import './part.css'
import Footer from '../footer/Footer'
import Cadgallery from './singledesign/SingleDesign';
import m1 from '../img/mobile1.png'
import m2 from '../img/mobile2.png'
import m3 from '../img/mobile3.png'
import { useParams } from 'react-router-dom';
import { Spinner } from 'react-bootstrap';
import axios from 'axios';
const CadG = () => {
    const data = [
        { 
            'id':1,
            'post_image': car,
            'profile_image': car,
            'post_on' : '2 feb 2020',
            'insta_likes': '2.7k',
            'community_likes': '290',
            'group_likes': '123',
            'factory_likes': '190'
        },
        { 
            'id':2,
            'post_image': car,
            'profile_image': car,
            'post_on' : '2 feb 2020',
            'insta_likes': '2.7k',
            'community_likes': '290',
            'group_likes': '123',
            'factory_likes': '190'
        },
        {
            'id':3,
            'post_image': car,
            'profile_image': car,
            'post_on' : '2 feb 2020',
            'insta_likes': '2.7k',
            'community_likes': '290',
            'group_likes': '123',
            'factory_likes': '190'
        },
        {
            'id':4,
            'post_image': car,
            'profile_image': car,
            'post_on' : '2 feb 2020',
            'insta_likes': '2.7k',
            'community_likes': '290',
            'group_likes': '123',
            'factory_likes': '190'
        },
        {
            'id':5,
            'post_image': car,
            'profile_image': car,
            'post_on' : '2 feb 2020',
            'insta_likes': '2.7k',
            'community_likes': '290',
            'group_likes': '123',
            'factory_likes': '190'
        },
        {
            'id':6,
            'post_image': car,
            'profile_image': car,
            'post_on' : '2 feb 2020',
            'insta_likes': '2.7k',
            'community_likes': '290',
            'group_likes': '123',
            'factory_likes': '190'
        },
    ]
    const params = useParams()
    const[user, setUser] = useState()
    const[userSave, setUserSave] = useState()

    const [gridchange,setgridchange]=useState(0);
    const singleview=()=>{
         setgridchange(1);
    }
    const doubleview=()=>{
        setgridchange(2);
    }
    const tripleview=()=>{
        setgridchange(3);
    }

    const showsorting=()=>{
        let sortarr=document.querySelectorAll('.partsort');

        for(let i=0;i<3;i++){
            if(sortarr[i].style.display=='none'){
                sortarr[i].style.display='block'; 
            }
            else{
                sortarr[i].style.display='none';
            }
         
        }
        console.log(sortarr)
    }
    console.log(gridchange)

    const getUserProfileInfo=async()=>{
        const id = params.id
        try {
            const res = await fetch("/getCadAllImages", {
                method: "GET",
                headers: {
                    "content-type": "application/json"
                }
            })
            const data = await res.json()
            console.log(data)
            setUser(data)
            console.log(data)
        } catch (error) {
            console.error(error)
        }
    }

    const getCadUserSavedImages=async()=>{
        const id = params.id
        const result = await axios.get(`/getAllCadImages`)
        const {data} = result
        setUserSave(data)
    }

    useEffect(() => {
        getUserProfileInfo()
        getCadUserSavedImages()
    },[])


  return (
    <div>
        <Header />
        <div className='partgallery'>
         <div className='designheading'>
             <h1>CAD Gallery</h1>
         </div>
         <div className='mobilelayouts'>
             <div className='mobilesorting'>
                 <h3 onClick={showsorting}>
                    <svg width="24" height="16" viewBox="0 0 24 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M0 16V14H8V16H0ZM0 2V0H24V2H0ZM0 9V7H16V9H0Z" fill="#4F4F4F"/>
                    </svg>
                    Sort by
                  </h3> 
             </div>
             <div className='difflayouts'>
                 <div className='layimg'>
                    <img src={m1} alt='mobile' onClick={singleview}/>
                 </div>
                 <div className='layimg'>
                   <img src={m2} alt='mobile' onClick={doubleview}/>
                 </div>
                 <div className='layimg'>
                    <img src={m3} alt='mobile' onClick={tripleview}/>
                 </div>
             </div>
         </div>
         <div className='partsort'>
             <form>
                <label>Search</label>
                <input type='text' name='search' placeholder='Search on this website' className='pladjust'/>
             </form>
         </div>
         <div className='partsort'>
             <form>
                <label>Sort By:</label>
                <input type='text' name='search' placeholder='Standard Sorting' className='pladjust2'/>
                <input type='text' name='search' placeholder='Sort by project' className='pil-3'/>
             </form>
         </div>
         <div className='partsort'>
             <form>
                <label>Category</label>
                <input type='text' name='search' placeholder='main category'/>
                <input type='text' name='search' placeholder='Second category' className='pil-3' />
                <input type='text' name='search' placeholder='Third category' className='pil-3' />
                <input type='text' name='search' placeholder='Sub category' className='pil-3' />
             </form>
         </div>
         <div className={'mainpartgallery' + gridchange}>
           {user?user.Images.map(design=>(  
             <div className='designpart'>
                 <div className='mainimage'>
                      
                <Popup 
                    trigger={   
                        <img src={design.image} 
                            alt='galleryimage' 
                        />} 
                        position="right center"
                >
                    {close => (
                    <div>
                        <a className="close" onClick={close}>
                        &times;
                        </a>
                        <Cadgallery part={design}  />
                    </div>
                    )}
                    </Popup> 
                    <div className='imagehover'>
                        <div className='hoverhead'>
                            <div className='infosvg'>
                                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M11.9802 7.89987C12.4957 7.89987 12.9135 7.482 12.9135 6.96654C12.9135 6.45107 12.4957 6.0332 11.9802 6.0332C11.4647 6.0332 11.0469 6.45107 11.0469 6.96654C11.0469 7.482 11.4647 7.89987 11.9802 7.89987Z" fill="#FAFAFA"/>
                                    <path d="M14.0007 16.6671H12.6673V9.40039H10.6673C10.4905 9.40039 10.3209 9.47063 10.1959 9.59565C10.0709 9.72068 10.0007 9.89025 10.0007 10.0671C10.0007 10.2439 10.0709 10.4134 10.1959 10.5385C10.3209 10.6635 10.4905 10.7337 10.6673 10.7337H11.334V16.6671H10.0007C9.82384 16.6671 9.65427 16.7373 9.52925 16.8623C9.40422 16.9873 9.33398 17.1569 9.33398 17.3337C9.33398 17.5105 9.40422 17.6801 9.52925 17.8051C9.65427 17.9302 9.82384 18.0004 10.0007 18.0004H14.0007C14.1775 18.0004 14.347 17.9302 14.4721 17.8051C14.5971 17.6801 14.6673 17.5105 14.6673 17.3337C14.6673 17.1569 14.5971 16.9873 14.4721 16.8623C14.347 16.7373 14.1775 16.6671 14.0007 16.6671Z" fill="#FAFAFA"/>
                                    <path d="M11.9987 22.6663C9.88904 22.6663 7.82674 22.0408 6.07262 20.8687C4.3185 19.6966 2.95132 18.0307 2.14399 16.0816C1.33665 14.1326 1.12542 11.9878 1.53699 9.91872C1.94857 7.84959 2.96447 5.94897 4.45623 4.45721C5.94799 2.96545 7.84861 1.94955 9.91774 1.53797C11.9869 1.12639 14.1316 1.33763 16.0807 2.14496C18.0297 2.9523 19.6956 4.31947 20.8677 6.0736C22.0398 7.82772 22.6654 9.89001 22.6654 11.9997C22.6654 14.8287 21.5416 17.5418 19.5412 19.5422C17.5408 21.5425 14.8277 22.6663 11.9987 22.6663V22.6663ZM11.9987 2.66635C10.1527 2.66635 8.34824 3.21374 6.81338 4.2393C5.27852 5.26486 4.08224 6.72252 3.37583 8.42797C2.66941 10.1334 2.48458 12.01 2.84471 13.8205C3.20484 15.631 4.09375 17.2941 5.39904 18.5993C6.70433 19.9046 8.36737 20.7935 10.1779 21.1537C11.9883 21.5138 13.865 21.329 15.5704 20.6226C17.2759 19.9161 18.7335 18.7199 19.7591 17.185C20.7846 15.6501 21.332 13.8456 21.332 11.9997C21.332 9.52433 20.3487 7.15035 18.5984 5.40002C16.848 3.64968 14.4741 2.66635 11.9987 2.66635V2.66635Z" fill="#FAFAFA"/>
                                </svg>
                                <h6>Learn more</h6>

                            </div>
                            <div className='hoverheadright'>
                                <div className='bookmark'>
                                    <svg width="17" height="26" viewBox="0 0 17 26" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M16 25L8.5 15.6829L1 25V1H16V25Z" stroke="#FAFAFA" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                    </svg>
                                </div>
                                <div className='bookmark'>
                                  <a href={car} download>
                                    <i class="fa fa-download" aria-hidden="true"></i>
                                    </a>
                                 </div>
                            </div>
                        </div>
                        <div className='hoverbottom'>
                            <div className='hbottoml'>
                                <h4>Project Name</h4>
                            </div>
                            <div className='hbottomr'>
                                <div className='icons'>
                                    <svg width="34" height="35" viewBox="0 0 34 35" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M17.0307 9.87222C16.3348 8.46535 15.1641 7.34965 13.7254 6.72213C12.2867 6.09462 10.6725 5.99567 9.16797 6.44274C7.6634 6.88982 6.36519 7.85416 5.5026 9.16548C4.64001 10.4768 4.26853 12.0508 4.45375 13.6094C4.45375 17.8766 7.66987 23.4913 17.0307 29.2588C26.3198 23.5003 29.5628 17.8766 29.5628 13.6094C29.708 12.0732 29.3157 10.5342 28.4529 9.25492C27.5901 7.97565 26.3102 7.03534 24.8315 6.59438C23.3528 6.15342 21.7669 6.23913 20.3444 6.83688C18.9218 7.43463 17.7507 8.50741 17.0307 9.87222V9.87222Z" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                    </svg>
                                </div>
                                <div className='icons'>
                                    <svg width="32" height="33" viewBox="0 0 32 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M25.9991 4.5L18.5591 28.5L13.2534 17.2543L25.9991 4.5Z" stroke="#FAFAFA" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                        <path d="M26 4.5L2 11.94L13.2543 17.2543L26 4.5Z" stroke="#FAFAFA" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                    </svg>
                                </div>
                            
                            </div>
                        </div>
                     </div>
                 </div>
                 <div className='partbottom'>
                     <div className='partauthor'>
                       <h6>Author</h6>
                     </div>
                     <div className='particons'>
                        <div className='dicons'>
                           <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M10.0183 5.51255C9.60887 4.68498 8.92023 4.02869 8.07393 3.65956C7.22764 3.29044 6.27814 3.23223 5.39309 3.49521C4.50805 3.7582 3.74439 4.32546 3.23699 5.09683C2.72958 5.86819 2.51106 6.79404 2.62002 7.71088C2.62002 10.221 4.51185 13.5238 10.0183 16.9164C15.4824 13.5291 17.3901 10.221 17.3901 7.71088C17.4755 6.80724 17.2447 5.90195 16.7372 5.14944C16.2296 4.39692 15.4767 3.8438 14.6069 3.58441C13.7371 3.32502 12.8042 3.37544 11.9674 3.72706C11.1306 4.07868 10.4417 4.70972 10.0183 5.51255Z" fill="#828282" stroke="#828282" stroke-width="3" stroke-linecap="round" stroke-linejoin="round"/>
                            </svg>

                        <h6>00</h6>
                        </div>
                        <div className='dicons'>
                          <Popup trigger={ <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M10.2857 1.14258C5.55301 1.14258 1.71423 4.49825 1.71423 8.63315C1.73566 9.73807 2.01006 10.8223 2.51526 11.7983C3.02046 12.7742 3.74229 13.6144 4.6224 14.2511C4.46165 15.7072 3.93878 17.0965 3.10403 18.2854C3.10403 18.2854 6.16525 17.7073 7.45097 15.7027C8.37092 15.9843 9.32566 16.1283 10.2857 16.13C15.0183 16.13 18.8571 12.7743 18.8571 8.63315C18.8571 4.49197 15.0183 1.14258 10.2857 1.14258Z" stroke="#4F4F4F" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                          </svg>} position="right center">
                                        {close => (
                                            
                                        <div>
                                            <a className="close" onClick={close}>
                                            &times;
                                            </a>
                                            <Cadgallery part={design}  />
                                        
                                        </div>
                                        )}
                             </Popup>

                        <h6>00</h6>
                        </div>
                     </div>
                 </div>
             </div>
             )):<div style={{
                    width: "90vw", 
                    height: "auto",
                    textAlign: "center"
                }}>
            <Spinner style={{}} animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
            </Spinner>
             </div>}
         </div>
        </div>
        <Footer />
    </div>
  )
}

export default CadG